.product-detail {
  position: relative;
  margin-top: -30px;

  .breadcrumbs {
    position: absolute;
    top: 40px;
    left: 0;
    right: 0;
    @media (max-width: $tablet) {
      top: 24px;
    }
    @media (max-width: $mobile) {
      position: relative;
      background: #f8f9f9;
      margin-top: -30px;
      padding-top: 30px;
    }
  }

  &__welcome {
    background-color: #F8F9F9;
    padding-top: 152px;
    @media (max-width: $tablet) {
      padding-top: 106px;
    }
    @media (max-width: $mobile) {
      padding-top: 20px;
    }
  }

  &__top-wrapper {
    display: grid;
    grid-template-columns: 26% 1fr 1fr;
    padding-bottom: 60px;
    @media (max-width: $laptop) {
      grid-template-columns: 40% 1fr;
      padding-bottom: 40px;
    }
  }

  &__picture {
    grid-column: 1;
    @media (max-width: $mobile) {
      grid-column: 1/3;
      grid-row: 1;
      margin-bottom: 20px;
    }

    img {
      max-width: 100%;
    }
  }

  &__main {
    grid-column: 2;
    margin-left: 84px;
    padding-top: 24px;
    @media (max-width: 1440px) {
      margin-left: 40px;
      padding-top: 0;
    }
    @media (max-width: $tablet) {
      margin-left: 32px;
    }
    @media (max-width: $mobile) {
      grid-row: 2;
      grid-column: 1/3;
      margin-left: 0;
    }
  }

  &__top-props {
    grid-column: 3;
    margin-left: 60px;
    padding-top: 26px;
    @media (max-width: 1440px) {
      margin-left: 40px;
    }
    @media (max-width: $laptop) {
      grid-column: 1/3;
      grid-row: 2;
      margin-left: 0;
      padding-top: 32px;
    }
    @media (max-width: $mobile) {
      grid-row: 3;
      grid-column: 1/3;
    }
  }

  &__title {
    margin-bottom: 24px;
    @media (max-width: $tablet) {
      font-size: 24px;
      font-weight: 700;
      line-height: 28.8px;
    }
  }

  &__label {
    padding: 8px 10px;
    background-color: #EDA01E;
    color: #fff;
    font-size: 14px;
    font-weight: 600;
    line-height: 17.07px;
    letter-spacing: 0.008em;
    border-radius: 6px;
    vertical-align: middle;
    white-space: nowrap;
    margin-left: 10px;
    @media (max-width: $tablet) {
      padding: 5px 10px;
    }
  }

  &__main {
    display: flex;
    flex-direction: column;
  }

  &__subtitle {
    display: block;
    font-size: 20px;
    font-weight: 600;
    line-height: 24px;
    margin-bottom: 8px;
    color: $black;
    @media (max-width: $tablet) {
      font-size: 16px;
      font-weight: 500;
      line-height: 24px;
    }
  }

  &__include {
    font-size: 16px;
    font-weight: 500;
    line-height: 24px;
    color: #6B7280;
    margin-bottom: auto;
  }

  &__buy-logic {
    margin-bottom: 60px;
    @media (max-width: $tablet) {
      margin-bottom: 0;
    }
  }

  &__price {
    font-size: 30px;
    font-weight: 600;
    line-height: 39px;
    letter-spacing: 0.008em;
    margin-bottom: 24px;
    margin-top: 20px;
    @media (max-width: $tablet) {
      font-size: 20px;
      font-weight: 700;
      line-height: 26px;
      letter-spacing: 0.008em;
      margin-bottom: 20px;
    }

    span {
      font-size: 50px;
      font-weight: 600;
      line-height: 65px;
      letter-spacing: 0.008em;
      @media (max-width: $tablet) {
        font-size: 28px;
        font-weight: 700;
        line-height: 36.4px;
        letter-spacing: 0.008em;
      }
    }
  }

  &__buy-logic {
    max-width: 410px;
    @media (max-width: $mobile) {
      max-width: 100%;
    }
  }

  .amount-logic {
    background-color: #fff;

    &__less {
      background-color: #fff;
    }

    &__more {
      background-color: #fff;
    }
  }

  &__btns {
    @media (max-width: $tablet) {
      display: flex;
    }
    @media (max-width: $mobileMedium) {
      flex-direction: column;
    }
  }

  &__add-btn {
    width: 100%;
    text-align: center;
    margin-top: 8px;
    @media (max-width: $tablet) {
      margin-top: 0;
      margin-left: 8px;
    }
    @media (max-width: $mobileMedium) {
      margin-left: 0;
      margin-top: 8px;
    }
  }

  &__expire {
    font-size: 16px;
    font-weight: 400;
    line-height: 24px;
    color: #6B7280;
    margin-bottom: 36px;
    @media (max-width: $tablet) {
      margin-bottom: 24px;
    }
  }

  &__values {
    display: flex;
    flex-wrap: wrap;
    margin: 0 -12px 24px;
    @media (max-width: $mobileMedium) {
      margin: 0 -4px 8px;
    }

    .product-val-block {
      width: calc(25% - 24px);
      flex-grow: 1;
      margin: 0 12px 12px;
      @media (max-width: $mobileMedium) {
        margin: 0 4px 8px;
      }
    }
  }

  &__vitamins {
    display: flex;
    flex-wrap: wrap;
    margin: -5.5px;
    @media (max-width: $mobileMedium) {
      margin: -4px;
    }

    span {
      display: inline-flex;
      justify-content: center;
      align-items: center;
      flex-shrink: 0;
      width: 35px;
      height: 35px;
      font-size: 16px;
      font-weight: 700;
      line-height: 24px;
      color: #fff;
      border-radius: 50%;
      margin: 5.5px;
      @media (max-width: $mobileMedium) {
        width: 30px;
        height: 30px;
        font-size: 14px;
        line-height: 21px;
        margin: 4px;
      }
    }
  }

}

.product-info-blocks {
  margin-top: 30px;
  @media (max-width: $tablet) {
    margin-top: 16px;
  }

  &__tabs {
    display: flex;
    margin: 0 -12px;
    overflow-x: auto;
  }

  &__tab {
    position: relative;
    font-size: 20px;
    font-weight: 600;
    line-height: 24px;
    cursor: pointer;
    padding-bottom: 10px;
    margin: 0 12px;
    white-space: nowrap;
    @include hover-effect;
    @media (max-width: $tablet) {
      font-size: 18px;
      font-weight: 500;
      line-height: 27px;
    }

    &[data-active] {
      cursor: auto;

      &:hover {
        opacity: 1;
      }

      &::after {
        content: '';
        position: absolute;
        display: inline-block;
        background-color: #1EB2E8;
        height: 3px;
        width: 100%;
        left: 0px;
        bottom: 0px;
      }
    }
  }

  &__box {
    padding-top: 50px;
    display: none;
    @media (max-width: $tablet) {
      padding-top: 24px;
    }

    &[data-active] {
      display: block;
    }
  }
}

.product-val-block {
  display: flex;
  flex-direction: column;
  padding: 20px;
  border: 1px solid #E9ECF2;
  border-radius: 15px;
  text-align: center;
  min-width: 124px;
  @media (max-width: $tablet) {
    padding: 14px;
  }
  @media (max-width: $mobileMedium) {
    min-width: 80px;
    padding: 15px 11px;
  }

  &__title {
    font-size: 24px;
    font-weight: 600;
    line-height: 28.8px;
    margin-bottom: 6px;
    @media (max-width: $tablet) {
      font-size: 18px;
      font-weight: 500;
      line-height: 21.6px;
    }
  }

  &__value {
    color: #6B7280;
    @media (max-width: $tablet) {
      font-size: 16px;
      font-weight: 500;
      line-height: 24px;
    }
    @media (max-width: $mobileMedium) {
      font-size: 14px;
      font-weight: 400;
      line-height: 21px;
    }
  }
}

.product-description {
  display: flex;
  @media (max-width: $laptop) {
    flex-wrap: wrap;
  }


  &__title {
    font-size: 32px;
    font-weight: 700;
    line-height: 38.4px;
    margin-bottom: 34px;
    @media (max-width: $tablet) {
      margin-bottom: 24px;
    }
    @media (max-width: $mobile) {
      font-size: 24px;
      font-weight: 700;
      line-height: 28.8px;
      margin-bottom: 16px;
    }
  }

  &__text {
    @media (max-width: $tablet) {
      font-size: 16px;
      font-weight: 500;
      line-height: 24px;
      text-align: left;
    }

    p {
      margin-bottom: .5em;
      @media (max-width: $tablet) {
        &:last-child {
          margin-bottom: 0;
        }
      }
    }
  }

  &__pictures {
    width: 46%;
    flex-shrink: 0;
    margin-left: 8%;
    height: 24vw;
    @media (max-width: $laptop) {
      width: 100%;
      margin-left: 0;
      margin-top: 24px;
      height: 70vw;
    }
    @media (max-width: $mobile) {
      margin-top: 20px;
      height: auto;
    }

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }

  }


  &__sliders {
    display: flex;
    height: 100%;
    width: 100%;
    @media (max-width: $laptop) {
      height: 70vw;
    }
    @media (max-width: $mobile) {
      flex-direction: column-reverse;
      height: auto;
    }
  }

  &__thumb-slider {
    width: 142px;
    flex-shrink: 0;
    @media (max-width: $laptop) {
      width: 20vw;
    }
    @media (max-width: $mobile) {
      width: 100%;
      margin-top: 8px;
    }

    .swiper {
      height: 24vw;
      @media (max-width: $laptop) {
        height: 70vw;
      }
      @media (max-width: $mobile) {
        height: auto;
      }
    }

    .swiper-slide {
      border-radius: 15px;
      overflow: hidden;
      cursor: pointer;
      @include hover-effect;

      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
        object-position: center;

      }
    }
  }

  &__big-slider {
    position: relative;
    height: 100%;
    width: calc(100% - 154px);
    margin-left: auto;
    @media (max-width: $laptop) {
      width: calc(100% - 20vw - 12px);
    }
    @media (max-width: $mobile) {
      width: 100%;
    }

    .seasons-nav__arrow {
      position: absolute;
      z-index: 2;
      top: 50%;
      transform: translateY(-50%);
      @media (max-width: $mobileMedium) {
        display: none;
      }

      &--left {
        left: 15px;
      }

      &--right {
        right: 15px;
      }
    }


    .swiper-slide {
      border-radius: 15px;
      overflow: hidden;
      @media (max-width: $mobileMedium) {
        height: 64vw;
      }

      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
        object-position: center;
      }
    }

    .swiper {
      width: 100%;
      height: 100%;
    }
  }
}


.structure {
  &__block {
    & + .structure__block {
      margin-top: 32px;
    }
  }
}

.structure-block {
  &__title {
    font-size: 28px;
    font-weight: 700;
    line-height: 33.6px;
    margin-bottom: 20px;
    @media (max-width: $tablet) {
      margin-bottom: 16px;
    }
  }

  &__subtitle {
    font-size: 20px;
    font-weight: 600;
    line-height: 24px;
    margin-bottom: 16px;
  }

  &__list {
    display: flex;
    flex-wrap: wrap;
    margin: 0 -20px -16px;
    @media (max-width: $mobile) {
      margin: 0 0 -16px;
    }
  }

  &__item {
    display: flex;
    justify-content: space-between;
    width: calc(50% - 40px);
    margin: 0 20px 16px;
    color: #6B7280;
    border-bottom: 1px solid #E9ECF2;
    font-size: 16px;
    font-weight: 500;
    line-height: 24px;
    @media (max-width: $mobile) {
      margin: 0 0 16px;
      width: 100%;
      &:last-child {
        margin-bottom: 0;
      }
    }

    span:nth-child(2) {
      color: $black;
    }
  }

  &__content {
    display: flex;
    flex-wrap: wrap;
    margin: 0 -30px -16px;
    background-color: #FAFAFA;
    padding: 20px;
    border-radius: 15px;
    padding-bottom: 0;
    @media (max-width: $tablet) {
      width: 100%;
      margin: 0 0 -32px;
      padding: 15px;
      padding-bottom: 0;
    }
    @media (max-width: $mobile) {
      margin-bottom: 0;
      padding-bottom: 15px;
    }
  }

  &__box {
    width: calc(50% - 60px);
    margin: 0 30px 16px;
    @media (max-width: $tablet) {
      width: 100%;
      margin: 0 0 32px;
      & + .structure-block__box {
        margin-top: 32px;
      }
    }
  }
}

.product-reviews {
  &__wrapper {
    display: flex;
    @media (max-width: $tablet) {
      flex-wrap: wrap;
    }
  }

  &__title {
    font-size: 32px;
    font-weight: 700;
    line-height: 38.4px;
    margin-bottom: 38px;
    @media (max-width: $tablet) {
      font-size: 28px;
      font-weight: 700;
      line-height: 33.6px;
      margin-bottom: 24px;
    }
    @media (max-width: $mobile) {
      font-size: 24px;
      font-weight: 700;
      line-height: 30.24px;
    }
  }

  &__stars {
    display: flex;
    flex-direction: column;
    width: 27%;
    flex-shrink: 0;
    margin-right: 5%;
    @media (max-width: $tablet) {
      width: 100%;
      margin-right: 0;
      margin-bottom: 24px;
    }


    .prod-rev-star + .prod-rev-star {
      margin-top: 16px;
      @media (max-width: $tablet) {
        margin-top: 12px;
      }
    }
  }

  &__items {
    .product-review-item + .product-review-item {
      margin-top: 16px;
    }
  }

  &__buttons {
    display: flex;
    justify-content: center;
    margin-top: 32px;
    gap: 12px;
    @media (max-width: $tablet) {
      margin-top: 24px;
    }

    .btn {
      white-space: nowrap;
    }
  }
}

