 5.78599L9.01513 2.00564Z' fill='%236B7280'/%3e%3c/svg%3e ");
    }
  }

  &__count {
    color: #6B7280;
  }

  &__line {
    position: relative;
    flex-grow: 1;
    margin-left: 8px;
    height: 8px;
    background: #E9ECF2;
    border-radius: 100px;
    margin-right: 8px;

    span {
      position: absolute;
      top: 0;
      left: 0;
      bottom: 0;
      border-radius: 100px;
      background: #1EB2E8;
    }
  }
}

.product-review-item {
  padding: 16px;
  background-color: #F8F9F9;
  border-radius: 15px;
  font-size: 18px;
  font-weight: 500;
  line-height: 27px;
  @media (max-width: $tablet) {
    padding: 12px;
  }
  @media (max-width: $mobile) {
    font-size: 16px;
    line-height: 24px;
  }

  &__title-block {
    color: #6B7280;
    margin-bottom: 12px;
    @media (max-width: $mobile) {
      margin-bottom: 8px;
    }

    span {
      color: #283238;
    }
  }

  &__description {
    font-weight: 500;
  }

  &__stars {
    display: flex;
    align-items: center;
    margin-bottom: 8px;

    span {
      display: flex;
    }

    span + span {
      margin-left: 5px;
    }
  }
}

