.breadcrumbs {
  margin: 32px 0 24px;
  color: #6B7280;

  @media (max-width: $tablet) {
    margin: 24px 0;
    font-size: 16px;
    line-height: 20px;
  }

  &__list {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
  }

  &__item {
    &:last-child {
      &::before {
        opacity: .6;
      }
    }

    & + .breadcrumbs__item {
      position: relative;
      display: flex;
      align-items: center;


      &::before {
        content: '';
        width: 21px;
        height: 21px;
        display: inline;
        background-image: url("data:image/svg+xml;charset=UTF-8,%3csvg width='21' height='21' viewBox='0 0 21 21' fill='none' xmlns='http://www.w3.org/2000/svg'%3e%3cpath fill-rule='evenodd' clip-rule='evenodd' d='M8.77463 6.9467C9.05648 6.64316 9.53103 6.62559 9.83456 6.90744L13.3346 10.1574C13.4874 10.2993 13.5742 10.4985 13.5742 10.707C13.5742 10.9156 13.4874 11.1147 13.3346 11.2566L9.83456 14.5066C9.53103 14.7885 9.05648 14.7709 8.77463 14.4674C8.49277 14.1638 8.51035 13.6893 8.81388 13.4074L11.722 10.707L8.81388 8.00663C8.51035 7.72478 8.49277 7.25023 8.77463 6.9467Z' fill='%239CA5AD'/%3e%3c/svg%3e ");
      }
    }
  }

  a, span {
    display: flex;
    padding: 4px 6px;
  }

  a {
    @include hover-effect;
  }
}