.lk-pages {

}

.lk {
  margin: -30px 0 0px;
  padding-top: 60px;
  padding-bottom: 100px;
  background: #F8F9F9;
  overflow: hidden;

  @media (max-width: $tablet) {
    padding: 50px 0 40px;
  }
  @media (max-width: $mobileMedium) {
    margin-top: -10px;
    padding: 22px 0 24px;
  }

  &__wrapper {
    display: flex;
    @media (max-width: $tablet) {
      margin-bottom: 74px;
    }
    @media (max-width: $mobileMedium) {
      margin-bottom: 50px;
    }
  }

  &__menu {
    width: 220px;
    flex-shrink: 0;
    margin-right: 50px;
    @media (max-width: $tablet) {
      display: none;
    }
  }

  &__content {
    width: calc(100% - 270px);
    flex-grow: 1;
    @media (max-width: $tablet) {
      width: 100%;
    }
  }

  &__top-banners {
    display: flex;
    margin: 0 -12px;
    @media (max-width: $tablet) {
      margin: 0 -6px;
    }
    @media (max-width: $mobile) {
      flex-direction: column;
      margin: 0 0 -8px;
    }

    .lk-banner {
      width: calc((100% - 72px) / 3);
      margin: 0 12px;
      flex-grow: 1;
      @media (max-width: $tablet) {
        width: calc((100% - 36px) / 3);
        margin: 0 6px;
      }
      @media (max-width: $mobile) {
        width: 100%;
        margin: 0 0 8px;
      }
    }
  }

  &__top-banners {
    & + .lk__orders-list {
      margin-top: 50px;
    }
  }

  &__orders-list {
    & + .lk__orders-list {
      margin-top: 40px;
    }
  }
}

.lk-menu-item {
  display: flex;
  align-items: center;
  padding: 12px;
  border-radius: 15px;
  cursor: pointer;
  @include hover-effect;

  & + .lk-menu-item {
    margin-top: 18px;
  }

  &[data-active] {
    background-color: #fff;
    color: $black;
    cursor: auto;

    &:hover {
      opacity: 1;
    }

    .lk-menu-item__icon {
      background-color: #1EB2E8;

      svg path {
        fill: #fff;
      }
    }
  }

  &__icon {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 30px;
    height: 30px;
    background-color: #fff;
    box-shadow: 0px 3.5px 5.5px 0px #00000005;
    border-radius: 10px;
  }

  &__text {
    margin-left: 12px;
  }
}

.lk-banner {
  position: relative;
  padding: 24px;
  box-shadow: 0px 3.5px 5.5px 0px #00000005;
  background-color: #fff;
  border-radius: 15px;
  @media (max-width: $laptop) {
    padding: 18px;
  }
  @media (max-width: $tablet) {
    padding: 12px
  }

  &__floating-link {
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    right: 24px;
    top: 24px;
    background: #F6F7F8;
    width: 34px;
    height: 34px;
    border-radius: 8px;
    @media (max-width: $tablet) {
      right: 12px;
      top: 12px;
    }
  }

  &__wrapper {
    display: flex;
    align-items: center;
    @media (max-width: $laptop) {
      flex-direction: column;
      align-items: flex-start;
    }
  }

  &__list {
    display: flex;
    flex-wrap: wrap;
    margin: -10px;
    @media (max-width: $tablet) {
      flex-direction: column;
      margin: -5px;
    }
    @media (max-width: $mobile) {
      flex-direction: row;
    }
  }

  &__top {
    margin-bottom: 12px;
  }

  &__icon {
    flex-shrink: 0;
    margin-left: auto;
    @media (max-width: $laptop) {
      margin: 20px auto 0;
    }
  }

  &__ava {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 46px;
    height: 46px;
    border-radius: 50%;
    background-color: #1EB2E8;
    margin-bottom: 20px;
    @media (max-width: $laptop) {
      width: 34px;
      height: 34px;
      margin-bottom: 16px;
      svg {
        max-width: 18px;
      }
    }
    @media (max-width: $tablet) {
      width: 27px;
      height: 27px;
      margin-bottom: 12px;
      svg {
        max-width: 15px;
      }
    }

    &--yellow {
      background-color: #F0C274;
    }

    &--red {
      background-color: #E89271;
    }

    &--violet {
      background-color: #D398E7;
    }
  }

  &__title {
    font-size: 18px;
    font-weight: 500;
    line-height: 27px;
    @media (max-width: $laptop) {
      font-size: 16px;
      line-height: 24px;
      margin-bottom: 14px;
    }
    @media (max-width: $tablet) {
      font-size: 14px;
      line-height: 21px;
      margin-bottom: 10px;
    }
  }

  &__item {
    margin: 10px;
    @media (max-width: $tablet) {
      margin: 5px;
    }

    span {
      &:nth-child(1) {
        display: block;
        font-size: 12px;
        font-weight: 400;
        line-height: 16.8px;
        color: #9CA5AD;
      }

      &:nth-child(2) {
        display: block;
        font-size: 14px;
        font-weight: 400;
        line-height: 21px;
      }
    }
  }

  &__price {
    font-size: 24px;
    font-weight: 800;
    line-height: 28.8px;
    @media (max-width: $tablet) {
      font-size: 16px;
      font-weight: 700;
      line-height: 19.2px;
    }
  }

  circle-progress {
    @media (max-width: $tablet) {
      display: none;
    }

    &::part(base) {
      transform: rotate(-100deg);
      transform-origin: center;
      width: 145px;
      height: 145px;
      @media (max-width: 1500px) {
        width: 100px;
        height: 100px;
      }
    }

    &::part(circle) {
      stroke: #f3e1d9;
      stroke-width: 10px;
    }

    &::part(value) {
      stroke: #e89a7d;
      stroke-linecap: round;
      stroke-width: 10px;
    }

    &::part(text) {
      font-size: 30.21px;
      font-weight: 700;
      line-height: 41.2px;
      letter-spacing: -0.02em;
      transform: rotate(100deg);
      transform-origin: center;
      fill: $black;
      @media (max-width: 1500px) {
        font-size: 24.21px;
        line-height: 36.2px;
      }
    }
  }
}

.orders-list {
  @media (max-width: $laptop) {
    display: flex;
    flex-direction: column;
  }

  &__title {
    margin-bottom: 24px;
    @media (max-width: $tablet) {
      font-size: 28px;
      font-weight: 700;
      line-height: 33.6px;
      margin-bottom: 20px;
    }
    @media (max-width: $mobile) {
      font-size: 24px;
      line-height: 28.8px;
      margin-bottom: 12px;
    }
  }

  &__btn {
    display: none;
    @media (max-width: $laptop) {
      display: inline-block;
      margin: 24px auto 0;
    }
  }

  &__services {
    margin-bottom: 24px;
  }

  &--in-active {
    .orders-list {
      &__title {
        margin-bottom: 8px;
      }

      &__title-block {
        margin-bottom: -20px;
        @media (max-width: 1700px) {
          margin-bottom: 20px;
        }
      }

      &__amount {
        font-size: 16px;
        font-weight: 500;
        line-height: 24px;
        color: #6B7280;
      }
    }
  }
}

.order-row {
  position: relative;
  display: flex;
  border-bottom: 1px solid #E9ECF2;
  @media (max-width: $laptop) {
    flex-wrap: wrap;
    padding: 16px;
    border: none;
    border-radius: 15px;
    background-color: #fff;
  }

  &:not(.orders-table__title-row) {

    @media (max-width: $laptop) {
      & + .order-row {
        margin-top: 12px;
      }
    }

    .order-row__id {
      @media (max-width: $laptop) {
        display: none;
      }
    }

    .order-row__num {
      @media (max-width: $laptop) {
        order: -4;
        width: 100%;
        display: flex;
        flex-direction: row;
        text-decoration: none;
      }

      &:hover {
        @media (min-width: 1241px) {
          color: #1EB2E8;
        }
      }

      @media (min-width: 1241px) {
        text-decoration: underline;
      }
    }

    .order-row__delivery-type {
      text-decoration: underline;
      @media (max-width: $tablet) {
        text-decoration: none;
      }
    }

    .order-row__additional {
      @media (max-width: $laptop) {
        position: absolute;
        top: 16px;
        right: 16px;
        width: 24px;
        height: 24px;
      }
    }

    .order-row__price {
      white-space: nowrap;
      @media (max-width: $laptop) {
        order: 4;
        width: 100%;
      }
    }

  }

  & > div {
    position: relative;
    display: flex;
    align-items: center;
    flex-shrink: 0;
    padding: 26px 20px;
    font-size: 14px;
    font-weight: 400;
    line-height: 20px;
    color: #6B7280;
    background-color: #fff;

    @media (max-width: $laptop) {
      flex-direction: column;
      align-items: flex-start;
      padding: 0;
      color: $black;
      margin: 6px 0;
      background-color: transparent;
      &::before {
        content: attr(data-name);
        display: block;
        color: #9CA5AD;
      }
    }

  }

  .order-row__id {
    width: 75px;
  }

  &__additional {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 60px;
    cursor: pointer;
    @media (max-width: $laptop) {
      display: none;
    }
  }

  .order-row__num {
    width: 142px;
    @media (max-width: $laptop) {
      font-size: 16px;
      font-weight: 600;
      line-height: 24px;
      &::before {
        color: $black;
        margin-right: 5px;
      }
    }
  }

  .order-row__date {
    width: 132px;
    @media (max-width: $laptop) {
      display: none;
    }
  }

  .order-row__delivery-date {
    width: 144px;
    @media (max-width: $laptop) {
      width: 20%;
      order: -3;
    }
  }

  .order-row__delivery-type {
    width: 150px;
    @media (max-width: 1440px) {
      display: none;
    }
    @media (max-width: $laptop) {
      display: block;
      width: 20%;
      order: -1;
    }
  }

  .order-row__address {
    width: 245px;
    @media (max-width: 1440px) {
      width: 23%;
    }
    @media (max-width: $laptop) {
      width: calc(80% - 20px);
      margin-left: auto;
    }
  }

  .order-row__conditions {
    width: 245px;
    @media (max-width: $laptop) {
      width: calc(80% - 20px);
      order: -3;
      margin-left: auto;
    }
  }

  .order-row__price {
    width: 137px;
    @media (max-width: $laptop) {
      font-size: 16px;
      font-weight: 700;
      line-height: 19.36px;
      &::before {
        font-size: 14px;
        font-weight: 400;
        line-height: 21px;
      }
    }
  }

  .order-row__status {
    flex-grow: 1;
    @media (max-width: 1440px) {
      width: 20%;
    }
    @media (max-width: $laptop) {
      width: 100%;
      order: -5;
      color: #6B7280;
    }
  }

  & > div[class*='order-row__'] {
    @media (max-width: $mobileMedium) {
      width: 100%;
      margin-left: 0;
    }
  }
}

.orders-table {
  border: 1px solid #E9ECF2;
  border-bottom: none;
  box-shadow: 0px 1px 2px 0px #1018280D;
  border-radius: 15px 15px 0 0;
