.order-status {
  position: relative;
  display: flex;
  align-items: center;
  padding: 2px 6px;
  border: 1px solid #E9ECF2;
  border-radius: 6px;
  min-width: 104px;

  &::before {
    content: '';
    width: 6px;
    height: 6px;
    display: inline-block;
    border-radius: 50%;
    background-color: #17B26A;
    margin-right: 4px;
    flex-shrink: 0;
  }

  &--sent {
    &::before {
      background-color: #FF9D42;
    }
  }

  &--approved {
    &::before {
      background-color: #17B26A;
    }
  }
}

.orders-pagination {
  display: flex;
  align-items: center;
  padding: 12px 26px 16px;
  background-color: #fff;
  border: 1px solid #E9ECF2;
  border-top: none;
  border-radius: 0 0 15px 15px;

  @media (max-width: $laptop) {
    display: none;
  }

  &__pages {
    display: flex;
    margin: 0 auto;

    span {
      display: flex;
      justify-content: center;
      align-items: center;
      width: 40px;
      height: 40px;
      flex-shrink: 0;
      cursor: pointer;
      @include hover-effect;

      & + span {
        margin-left: 2px;
      }

      &[data-active] {
        background: #F6F7F8;
        border-radius: 8px;
        cursor: auto;
        pointer-events: none;
      }
    }
  }

  &__go-btn {
    position: relative;
    display: flex;
    align-items: center;
    border: 1px solid #E9ECF2;
    font-size: 14px;
    font-weight: 500;
    line-height: 21px;
    padding: 14px;
    border-radius: 14px;
    cursor: pointer;
    @include hover-effect;

    &--back {
      &::before {
        content: "";
        width: 16px;
        height: 16px;
        display: inline;
        margin-right: 4px;
