.order-settings-box {
  position: relative;

  &__icon {
    display: flex;
    justify-content: center;
    align-items: center;
  }

  &__list {
    position: absolute;
    border: 1px solid #E9ECF2;
    background-color: #fff;
    border-radius: 10px;
    right: 100%;
    left: -140px;
    top: 50%;
    transform: translateY(-50%);
    opacity: 0;
    z-index: -1;
    visibility: hidden;
    transition: opacity .3s;

    &[data-active] {
      opacity: 1;
      z-index: 1;
      visibility: visible;
    }

    a {
      display: block;
      padding: 6px 12px;
      color: $black;
      @include hover-effect;

      & + a {
        border-top: 1px solid #E9ECF2;
      }
    }
  }
}

.profile-data {
