.orders-services {
  display: flex;
  margin-bottom: 24px;
  @media (max-width: 1500px) {
    flex-wrap: wrap;
  }

  &__search {
    width: 257px;
    margin-left: auto;
    @media (max-width: 1500px) {
      margin-left: 0;
      order: -2;
      width: 40%;
    }
    @media (max-width: $mobile) {
      flex-grow: 1;
      margin-right: 12px;
    }
    @media (max-width: $mobileMedium) {
      width: 100%;
      margin-right: 0;
      margin-bottom: 0;
      margin-top: 12px;
    }

    .form-input--search {
      input {
        background: #fff;
      }
    }
  }

  &__filters {
    margin-left: 12px;
    @media (max-width: 1500px) {
      width: 100%;
      margin-left: 0;
      margin-top: 15px;
    }
    @media (max-width: $mobileMedium) {
      margin-top: 12px;
    }

    .swiper-slide {
      width: 200px;
    }

    .swiper {
      overflow: unset;
    }
  }

  &__new-order {
    margin-left: 12px;
    @media (max-width: 1500px) {
      margin-left: auto;
      order: -1;
    }
    @media (max-width: $mobileMedium) {
      order: -3;
      width: 100%;
    }
  }

}

.filter-sort {
  position: relative;
  font-size: 16px;
  font-weight: 500;
  line-height: 24px;
  width: 200px;
  white-space: nowrap;

  &[data-inner-checked] {
    .filter-sort__current {
      border: 1px solid #1eb2e8;
      color: #1eb2e8;
    }
  }

  &__current {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 9.5px 12px;
    border: 1px solid #E9ECF2;
    border-radius: 15px;
    overflow: hidden;
    background-color: #fff;
    cursor: pointer;
    font-size: 14px;
    font-weight: 400;
    line-height: 21px;

    &::after {
      content: '';
      background-image: url("data:image/svg+xml;charset=UTF-8,%3csvg width='21' height='21' viewBox='0 0 21 21' fill='none' xmlns='http://www.w3.org/2000/svg'%3e%3cpath fill-rule='evenodd' clip-rule='evenodd' d='M4.80636 7.91452C5.10599 7.62851 5.58073 7.63955 5.86674 7.93918L10.5742 12.8708L15.2817 7.93918C15.5677 7.63955 16.0425 7.62851 16.3421 7.91452C16.6417 8.20052 16.6527 8.67527 16.3667 8.97489L11.1167 14.4749C10.9752 14.6232 10.7792 14.707 10.5742 14.707C10.3693 14.707 10.1732 14.6232 10.0317 14.4749L4.7817 8.97489C4.4957 8.67527 4.50674 8.20052 4.80636 7.91452Z' fill='%239CA5AD'/%3e%3c/svg%3e ");
      width: 20px;
      height: 20px;
      margin-left: 12px;
      flex-shrink: 0;
      transition: opacity .3s, transform .3s;
    }
  }

  &__body {
    position: absolute;
    top: calc(100% + 3px);
    left: 0;
    width: 100%;
    background-color: #fff;
    box-shadow: 0px 4px 5px -2px #18274B0F;
    border-radius: 15px;
    opacity: 0;
    z-index: -1;
    visibility: hidden;
    transform: translateY(-5px);
    transition: opacity .3s, transform .3s;

    &[data-active] {
      opacity: 1;
      z-index: 1;
      visibility: visible;
      transform: translateY(0);

      & + .filter-sort__current {
        &::after {
          transform: rotate(180deg);
        }
      }
    }
  }

  .form-input {
    label {
      margin-bottom: 0;
      padding: 8px 12px;
      width: 100%;
      flex-grow: 1;
      margin-left: 15px;
    }
  }

  &__item-to-choose {
    display: flex;
    padding: 0;
    flex-direction: column;
    cursor: pointer;
    @include hover-effect;

    &[data-active] {
      color: #1EB2E8;
      cursor: auto;

      &:hover {
        opacity: 1;
      }
    }
  }
}

.managers-row {
  position: relative;
  display: flex;
  border-bottom: 1px solid #E9ECF2;
  @media (max-width: $laptop) {
    padding: 16px;
    background: #fff;
    border-radius: 15px;
    & + .managers-row {
      margin-top: 12px;
    }
  }
  @media (max-width: $mobile) {
    flex-wrap: wrap;
