.managers-table {
  border: 1px solid #E9ECF2;
  border-bottom: none;
  box-shadow: 0px 1px 2px 0px #1018280D;
  border-radius: 15px 15px 0 0;
  @media (max-width: $laptop) {
    border-radius: 0;
    border: none;
    box-shadow: none;
  }
}

.manager-in-table {
  display: flex;
  align-items: center;

  &__ava {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-shrink: 0;
    color: #fff;
    font-size: 14px;
    font-weight: 400;
    line-height: 16.94px;
    width: 38px;
    height: 38px;
    margin-right: 12px;
    border-radius: 50%;
    background-color: #1EB2E8;
  }

  &__name {
    font-size: 14px;
    font-weight: 600;
    line-height: 19.1px;
    margin-bottom: 4px;
  }

  &__login {
    font-size: 14px;
    font-weight: 400;
    line-height: 16.94px;
    color: #6B7280;
  }

  &__info {
    display: flex;
    flex-direction: column;
  }
}

.lk-managers {
  margin-top: 50px;

  &__more {
    display: flex;
    justify-content: center;
    margin-top: 36px;
    @media (max-width: $tablet) {
      margin-top: 24px;
    }
    @media (max-width: $mobile) {
      margin-top: 16px;
    }
  }

  &__title-block {
    display: flex;
    align-items: center;
    margin-bottom: 26px;
    @media (max-width: $tablet) {
      justify-content: space-between;
    }

    @media (max-width: $mobileMedium) {
      .btn {
        width: 36px;
        height: 36px;

        span:not(.btn__icon) {
          display: none;
        }
      }
    }
  }

  &__title {
    margin-right: 24px;
    margin-bottom: 0;
  }
}


.order-detail {
  &__wrapper {
    display: grid;
    grid-template-columns: 66% 1fr;
  }

  &__go-back {
    @media (max-width: $mobileMedium) {
      display: none;
    }
  }

  &__main {
    margin-right: 24px;
    @media (max-width: 1360px) {
      grid-column: 1/3;
      grid-row: 1;
      margin-right: 0;
    }

  }

  &__sum {
    @media (max-width: 1360px) {
      grid-column: 1/3;
      grid-row: 3;
    }
  }

  &__table {
    grid-column: 1/3;
    margin-top: 32px;
    @media (max-width: 1360px) {
      margin-bottom: 32px;
    }
    @media (max-width: $mobileMedium) {
      margin-top: 20px;
      margin-bottom: 20px;
    }
  }

  &__buttons {
    display: flex;
    align-items: center;
    grid-column: 1/3;
    width: 100%;
    margin-top: 24px;
    @media (max-width: $mobileMedium) {
      .btn {
        white-space: nowrap;
        padding: 5px;
        flex-grow: 1;
      }
      .btn__icon {
        display: none;
      }
      .order-detail__cancel {
        margin-left: 12px;
      }
    }

    .btn:first-child {
      margin-right: 12px;
    }
  }

  &__cancel {
    margin-left: auto;
  }

}

.order-progress {
  position: relative;
  display: flex;
  justify-content: space-between;


  &::before {
    content: '';
    position: absolute;
    height: 8px;
    width: 100%;
    background-color: #F6F7F8;
    display: block;
  }

  &__step {
    position: relative;
    width: 30%;
    flex-shrink: 0;
    text-align: center;

    .order-progress__num {
      left: 50%;
      transform: translateX(-50%);
    }

    &:nth-child(1) {
      text-align: left;
      width: 20%;

      .order-progress__num {
        left: 13px;
        @media (max-width: $tablet) {
          left: 8px;
        }
      }
    }

    &:last-child {
      width: 20%;
      text-align: right;

      .order-progress__num {
        left: calc(100% - 13px);
        @media (max-width: $tablet) {
          left: calc(100% - 8px);
        }
      }
    }

    &--done {
      &::before {
        content: '';
        position: absolute;
        height: 8px;
        width: calc(100% + 4px);
        left: -4px;
        background-color: #1EB2E8;
        border-radius: 0 8px 8px 0;
        display: block;
      }

      .order-progress {
        &__num {
          color: #fff;
          background: #1EB2E8;
          border: 1px solid #1EB2E8;
        }
      }
    }
  }

  &__num {
    position: absolute;
    top: -15px;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 40px;
    height: 40px;
    color: #9CA5AD;
    font-size: 14px;
    font-weight: 400;
    line-height: 40px;
    border: 1px solid #E9ECF2;
    background-color: #F6F7F8;
    border-radius: 50%;
    @media (max-width: $tablet) {
      top: -11px;
      width: 30px;
      height: 30px;
      line-height: 30px;
    }
  }

  &__status {
    display: block;
    margin-top: 30px;
    font-size: 14px;
    font-weight: 400;
    line-height: 21px;
  }
}

.order-detail-main {
  background: #FFFFFF;
  border-radius: 15px;

  &__status {
    display: none;
    @media (max-width: $mobileMedium) {
      display: block;
      font-size: 12px;
      font-weight: 500;
      line-height: 18px;
      color: #6B7280;
      .order-status {
        display: inline-flex;
        margin-left: 12px;
        margin-top: 12px;
      }
    }
  }

  &__progress {
    margin: 0 24px 30px 24px;
    @media (max-width: $tablet) {
      margin: 0 16px 16px;
    }
    @media (max-width: $mobileMedium) {
      display: none;
    }
  }

  &__title {
    padding: 16px 24px;
    font-size: 24px;
    font-weight: 700;
    line-height: 28.8px;
    border-bottom: 1px solid #E9ECF2;
    @media (max-width: $tablet) {
      padding: 16px;
      font-size: 20px;
      font-weight: 600;
      line-height: 24px;
    }
  }

  &__props {
    position: relative;
    display: flex;
    justify-content: space-between;
    padding: 16px 24px 8px;
    @media (max-width: $tablet) {
      padding: 16px;
    }
    @media (max-width: $mobileMedium) {
      flex-wrap: wrap;
    }

    &::before {
      content: '';
      position: absolute;
      width: 1px;
      top: 30px;
      left: 50%;
      bottom: 20px;
      background-color: #E9ECF2;
      @media (max-width: $tablet) {
        bottom: 30px;
      }
      @media (max-width: $mobileMedium) {
        content: none;
      }
    }
  }

  &__block {
    position: relative;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    width: 44%;
    flex-shrink: 0;
    @media (max-width: $mobileMedium) {
      width: 100%;
    }

    &::before {
      content: '';
      position: absolute;
      width: 1px;
      top: 14px;
      left: 50%;
      bottom: 14px;
      background-color: #E9ECF2;
      @media (max-width: $mobileMedium) {
        content: none;
      }
    }
  }

  &__prop {
    position: relative;
    display: flex;
    flex-direction: column;
    margin-bottom: 24px;
    width: 44%;
    flex-shrink: 0;

    span:nth-child(1) {
      font-size: 16px;
      font-weight: 500;
      line-height: 24px;
      color: #9CA5AD;
      margin-bottom: 4px;
      @media (max-width: $tablet) {
        font-size: 14px;
        line-height: 21px;
      }
    }

    span:nth-child(2) {
      font-size: 18px;
      font-weight: 500;
      line-height: 27px;
      @media (max-width: $tablet) {
        font-size: 16px;
        line-height: 21px;
      }
    }
  }
}


.order-detail-sum {
  background: #FFFFFF;
  border-radius: 15px;

  &__title {
    display: flex;
    justify-content: space-between;
    padding: 16px 24px;
    font-size: 24px;
    font-weight: 700;
    line-height: 28.8px;
    border-bottom: 1px solid #E9ECF2;
    @media (max-width: $tablet) {
      font-size: 20px;
      font-weight: 600;
      line-height: 24px;
      padding: 16px;
    }
    @media (max-width: $mobileMedium) {
      padding:12px;
    }
  }

  &__box {
    padding: 16px 24px 30px;
    @media (max-width: $tablet) {
      padding: 16px;
    }
    @media (max-width: $mobileMedium) {
      padding:12px;
    }
  }

  &__item {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px 0;
    @media (max-width: $tablet) {
      padding: 6px 0;
    }

    & + .order-detail-sum__item {
      margin-top: 4px;
    }
  }

  &__name,
  &__value {
    width: 50%;
    flex-shrink: 0;
  }

  &__name {
    color: #9CA5AD;
    @media (max-width: $tablet) {
      font-size: 16px;
      font-weight: 500;
      line-height: 24px;
    }
    @media (max-width: $mobileMedium) {
      font-size: 14px;
      line-height: 20px;
    }
  }

  &__value {
    font-size: 18px;
    font-weight: 500;
    line-height: 21.6px;
    text-align: right;
    @media (max-width: $mobileMedium) {
      font-size: 14px;
      line-height: 20px;
    }
  }
}

.contains-row {
  position: relative;
  display: flex;
  border-bottom: 1px solid #E9ECF2;
  @media (max-width: $tablet) {
    flex-wrap: wrap;
    padding: 16px 16px 4px;
    background: #fff;
    border: 1px solid #E9ECF2;
    border-bottom: none;
    box-shadow: 0px 1px 2px 0px #1018280D;
    border-radius: 15px;
  }
  @media (max-width: $mobileMedium) {
    padding: 12px 12px 4px;
  }

  &__status {
    display: none;
    @media (max-width: $mobileMedium) {
      display: block;
    }
  }

  &__id {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-shrink: 0;
    background-color: #fff;
    font-size: 14px;
    font-weight: 400;
    line-height: 20px;
    text-align: center;
    width: 54px;
    color: #6B7280;
  }

  @media (max-width: $mobile) {
    flex-wrap: wrap;
  }

  &__col {
    position: relative;
    display: flex;
    align-items: center;
    flex-shrink: 0;
    padding: 26px 20px;
    font-size: 14px;
    font-weight: 400;
    line-height: 20px;
    width: 160px;
    flex-shrink: 0;
    color: #6B7280;
    background-color: #fff;
    @media (max-width: $tablet) {
      padding: 0;
      width: 25%;
      margin-bottom: 12px;

      &[data-name] {
        flex-direction: column;
        align-items: flex-start;
        justify-content: flex-start;

        &::before {
          content: attr(data-name);
          width: 100%;
          display: block;
          font-size: 14px;
          font-weight: 400;
          line-height: 21px;
          color: #9CA5AD;
        }
      }
    }
    @media (max-width: $mobileMedium) {
      width: 50%;
      color:$black;
    }

    &--weight {
      @media (max-width: $tablet) {
        display: none;
      }
    }

    &--title {
      span {
        display: none;
      }

      @media (max-width: $tablet) {
        color: #283238;
        font-size: 16px;
        font-weight: 600;
        line-height: 24px;
        margin-bottom: 10px;
        width: 100%;
        span {
          display: inline-block;
        }
      }
    }

    &:last-child {
      flex-grow: 1;
    }

    @media (max-width: $mobile) {
      margin-bottom: 12px;
    }

  }

  .contains-row__id {
    width: 75px;
    flex-shrink: 0;
    @media (max-width: $laptop) {
      display: none;
    }
  }

  &--title {
    @media (max-width: $tablet) {
      display: none;
    }

    .contains-row__id,
    .contains-row__col {
      color: $black;
      text-align: center;
      background-color: #F6F7F8;
    }
  }
}

.order-contains-table {
  border: 1px solid #E9ECF2;
  border-bottom: none;
  box-shadow: 0px 1px 2px 0px #1018280D;
  border-radius: 15px;
  overflow: hidden;
  overflow-x: auto;
  @media (max-width: $tablet) {
    box-shadow: none;
    border: none;
    .contains-row + .contains-row {
      margin-top: 12px;
    }
  }
}

.lk-deliveries {
  margin-top: 50px;

  &__title-block {
    display: flex;
    align-items: center;
  }

  &__title {
    margin-bottom: 0;
  }

  &__btn {
    margin-left: 24px;
  }
}

.lk-deliveries {
  &__title-block {
    margin-bottom: 26px;
    @media (max-width: $laptop) {
      display: flex;
      justify-content: space-between;
    }
  }

  &__btn {
    @media (max-width: $mobileMedium) {
      span:not(.btn__icon) {
        display: none;
      }
    }
  }

  &__items {
    display: flex;
    flex-wrap: wrap;
    margin: 0 -12px -30px;
    @media (max-width: $laptop) {
      margin: 0 0 -12px;
    }

    .lk-delivery {
      width: calc(50% - 24px);
      flex-shrink: 0;
      margin: 0 12px 30px;
      @media (max-width: $laptop) {
        width: 100%;
        margin: 0 0 12px;
      }
    }

  }
}

.lk-delivery {
  padding: 20px;
  background-color: #fff;
  border-radius: 15px;
  @media (max-width: $mobile) {
    padding: 12px;
  }

  &__content {
    display: flex;
    align-items: flex-start;
    @media (max-width: $mobile) {
      flex-wrap: wrap;
    }
  }

  &__info {
    margin-right: auto;
    @media (max-width: $mobile) {
      margin-right: 0;
      width: 100%;
    }
  }

  &__name {
    font-size: 20px;
    font-weight: 600;
    line-height: 24px;
    margin-bottom: 8px;
  }

  &__addr {
    max-width: 330px;
    margin-bottom: 16px;
  }

  &__addr {
    color: #6B7280;
  }

  &__map-btn {
    white-space: nowrap;
    margin-left: 20px;
    flex-shrink: 0;
    @media (max-width: $mobile) {
      margin-left: 0;
      margin-bottom: 16px;
    }
  }

  &__buttons {
    .btn + .btn {
      margin-left: 12px;
      @media (max-width: $mobileMedium) {
        margin-left: 6px;
      }
    }

    .btn {
      &__icon {
        display: none;
        @media (max-width: $mobileMedium) {
          display: block;
        }
      }

      span:not(.btn__icon) {
        @media (max-width: $mobileMedium) {
          display: none;
        }
      }
    }
  }
}