.catalog {
  @media (max-width: $mobile) {
    margin-top: 30px;
  }

  &__wrapper {
    display: flex;

    &--column {
      .catalog__items {
        @media (min-width: 768px) {
          margin: 0 0 -20px;
          .card-item {
            display: flex;
            flex-direction: row;
            width: 100%;
            margin: 0 0 20px;

            &__rating {
              position: absolute;
              top: 50%;
              left: 43.5%;
              transform: translate(-50%, -50%);
              @media (max-width: 1360px) {
                position: static;
                transform: none;
              }
            }

            .amount-logic {
              @media (max-width: 1360px) {
                margin-bottom: 0;
                margin-right: 8px;
              }
            }

            &__label {
              padding: 5px 7px;
              font-size: 10px;
              line-height: 12.1px;
            }

            &__info {
              position: relative;
              display: grid;
              grid-template-columns: 31% 25% 1fr 25%;
              @media (max-width: 1360px) {
                grid-template-columns: 1fr;
              }
            }

            &__main-info {
              grid-row: 2;
            }

            .card-item__price {
              text-align: right;
              grid-column: 3;
              grid-row: 1/3;
              margin: auto 0;
              margin-right: 30%;
              white-space: nowrap;
              @media (max-width: 1360px) {
                text-align: left;
                grid-column: 1/5;
                grid-row: 3;
                margin-top: 12px;
              }
            }

            .card-item__subinfo {
              margin-top: auto;
              margin-bottom: 8px;
            }

            &__buttons {
              grid-row: 1/3;
              grid-column: 4;
              margin: auto 0;
              @media (max-width: 1360px) {
                display: flex;
                grid-row: 4;
                grid-column: 1/5;
                margin-top: 12px;
              }
            }

            &__photo {
              padding-top: 20px;
              margin-bottom: 0;
              width: 180px;
              min-height: unset;
              margin-right: 55px;
              max-height: 238px;
              @media (max-width: 1360px) {
                width: 25%;
                flex-shrink: 0;
              }
            }

            &__description {
              margin-bottom: auto;
            }
          }
        }
      }
    }
  }

  &__filter {
    width: 250px;
    flex-shrink: 0;
    margin-right: 24px;
    @media (max-width: $tablet) {
      display: none;
    }
  }

  &__content {
    flex-grow: 1;
    @media (max-width: $tablet) {
      width: 100%;
    }
  }

  &__title {
    margin-bottom: 24px;
  }

  &__top-settings {
    display: flex;
    align-items: center;
    margin-bottom: 20px;
    @media (max-width: 1100px) {
      flex-wrap: wrap;
    }
  }

  &__sort {
    margin-left: auto;
    @media (max-width: 1100px) {
      margin-left: 0;
      margin-right: auto;
    }
  }

  .catalog__view {
    margin-left: 16px;
    @media (max-width: $mobile) {
      display: none;
    }
  }

  &__items {
    display: flex;
    flex-wrap: wrap;
    margin: 0 -8px -20px;
    @media (max-width: $mobileMedium) {
      width: 100%;
      margin: 0 0 -12px;
    }

    .card-item {
      &__photo {
        min-height: unset;
        height: 20vw;
        @media (max-width: $tablet) {
          height: 30vw;
        }
        @media (max-width: $mobileMedium) {
          height: 63vw;
        }
      }

      &__subinfo {
        flex-wrap: wrap;
      }
    }


    .card-item {
      width: calc(25% - 16px);
      margin: 0 8px 20px;
      @media (max-width: 1440px) {
        width: calc(33% - 16px);
      }
      @media (max-width: 1240px) {
        width: calc(50% - 16px);
      }
      @media (max-width: $mobileMedium) {
        width: 100%;
        margin: 0 0 12px;
      }
    }
  }

  .catalog__subcategories {
    @media (max-width: 1100px) {
      order: 2;
      width: 100%;
      max-width: unset;
      margin-top: 12px;
    }
  }

  &__more {
    display: flex;
    justify-content: center;
    margin-top: 32px;
    @media (max-width: $tablet) {
      margin-top: 24px;
    }
  }
}

.catalog-subcategories {
  display: flex;
  align-items: center;
  font-size: 16px;
  max-width: unset;
  width: 100%;
  margin-bottom: 12px;
  font-weight: 500;
  line-height: 24px;
  overflow-x: auto;
  max-width: calc(100% - 328px);

  &__item {
    display: inline-block;
    padding: 12px;
    border: 1px solid #E9ECF2;
    border-radius: 15px;
    white-space: nowrap;
    cursor: pointer;
    @include hover-effect;

    &[data-active] {
      background: #1EB2E8;
      color: #fff;
      cursor: auto;

      &:hover {
        opacity: 1;
      }
    }

    & + .catalog-subcategories__item {
      margin-left: 16px;
    }
  }
}

.catalog-view {
  display: flex;
  align-items: center;
  padding: 5px 7px;
  border: 1px solid #E0E4EA;
  border-radius: 15px;

  &__way {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 44px;
    height: 38px;
    cursor: pointer;

    &[data-active] {
      background: #E8F9FF;
      cursor: auto;

      svg path {
        stroke: #1EB2E8;
      }
    }
  }
}

.catalog-sort {
  position: relative;
  font-size: 16px;
  font-weight: 500;
  line-height: 24px;
  min-width: 200px;
  white-space: nowrap;
  background-color: #fff;

  &__current {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 12px;
    border: 1px solid #E9ECF2;
    border-radius: 15px;
    cursor: pointer;

    &::after {
      content: '';
      background-image: url("data:image/svg+xml;charset=UTF-8,%3csvg width='21' height='21' viewBox='0 0 21 21' fill='none' xmlns='http://www.w3.org/2000/svg'%3e%3cpath fill-rule='evenodd' clip-rule='evenodd' d='M4.80636 7.91452C5.10599 7.62851 5.58073 7.63955 5.86674 7.93918L10.5742 12.8708L15.2817 7.93918C15.5677 7.63955 16.0425 7.62851 16.3421 7.91452C16.6417 8.20052 16.6527 8.67527 16.3667 8.97489L11.1167 14.4749C10.9752 14.6232 10.7792 14.707 10.5742 14.707C10.3693 14.707 10.1732 14.6232 10.0317 14.4749L4.7817 8.97489C4.4957 8.67527 4.50674 8.20052 4.80636 7.91452Z' fill='%239CA5AD'/%3e%3c/svg%3e ");
      width: 20px;
      height: 20px;
      margin-left: 12px;
      flex-shrink: 0;
      transition: transform .3s;
      transform-origin: center;
    }
  }

  &__body {
    position: absolute;
    top: calc(100% + 3px);
    left: 0;
    width: 100%;
    background-color: #fff;
    box-shadow: 0px 4px 5px -2px #18274B0F;
    border-radius: 15px;
    opacity: 0;
    z-index: -1;
    visibility: hidden;
    transform: translateY(-5px);
    transition: opacity .3s, transform .3s;

    &[data-active] {
      opacity: 1;
      z-index: 1;
      visibility: visible;
      transform: translateY(0);

      & + .catalog-sort__current {
        &::after {
          transform: rotate(180deg);
        }
      }
    }
  }

  &__item-to-choose {
    padding: 8px 12px;
    cursor: pointer;
    @include hover-effect;

    &[data-active] {
      color: #1EB2E8;
      cursor: auto;

      &:hover {
        opacity: 1;
      }
    }
  }
}

.catalog-filter {
  &__block {
    padding: 16px;
    border: 1px solid #E9ECF2;
    border-radius: 16px;

    & + .catalog-filter__block {
      margin-top: 12px;
    }
  }

  &__subtitle {
    font-weight: 500;
    margin-bottom: 16px;
  }

  &__items {
    &[data-collapsed='true'] {
      .catalog-filter__item {

        &:nth-child(n+6) {
          display: none;
        }
      }
    }
  }

  &__more {
    color: #1EB2E8;
    text-decoration: underline;
    cursor: pointer;
    font-size: 14px;
    font-weight: 400;
    line-height: 21px;
    margin-top: 12px;
    @include hover-effect;
  }

  &__item {
    input {
      display: none;

      &:checked + label {
        color: #1EB2E8;

        &::after {
          content: '';
          background-image: url("data:image/svg+xml;charset=UTF-8,%3csvg width='11' height='9' viewBox='0 0 11 9' fill='none' xmlns='http://www.w3.org/2000/svg'%3e%3cpath d='M10.4065 0.261455C10.1866 0.0735871 9.90101 -0.0192547 9.61262 0.00333493C9.32423 0.0259246 9.05661 0.162097 8.86857 0.381924L4.1717 5.87497L1.6449 3.7656C1.54487 3.67955 1.42873 3.61424 1.30324 3.57347C1.17775 3.53271 1.0454 3.51729 0.913899 3.52812C0.782395 3.53895 0.654356 3.57581 0.537225 3.63657C0.420093 3.69732 0.316206 3.78074 0.231601 3.882C0.146997 3.98325 0.0833629 4.10032 0.044399 4.22638C0.00543504 4.35245 -0.00808177 4.485 0.00463411 4.61633C0.01735 4.74767 0.056045 4.87517 0.11847 4.99141C0.180895 5.10766 0.265804 5.21034 0.368262 5.29349L3.75357 8.12005C3.85796 8.20708 3.97915 8.27168 4.1096 8.30983C4.24004 8.34797 4.37695 8.35885 4.51178 8.34177C4.65477 8.33072 4.79418 8.29158 4.92203 8.22659C5.04988 8.1616 5.16366 8.07203 5.25686 7.96302L10.5263 1.79895C10.714 1.57888 10.8067 1.29334 10.784 1.00501C10.7614 0.716672 10.6253 0.449091 10.4056 0.260986' fill='white'/%3e%3c/svg%3e");
          left: 4.5px;
          width: 11px;
          display: inline-block;
          height: 9px;
          position: absolute;
        }

        &::before {
          background-color: #1EB2E8;
        }
      }
    }

    label {
      position: relative;
      display: flex;
      align-items: center;
      padding-left: 28px;
      cursor: pointer;
      color: #9CA5AD;

      &::before {
        content: '';
        position: absolute;
        left: 0;
        width: 20px;
        height: 20px;
        border: 1px solid #E0E4EA;
        border-radius: 4px;
      }
    }
  }
}

.catalog-sections {
  &__title {
    margin-bottom: 36px;
    @media (max-width: $tablet) {
      margin-bottom: 24px;
    }
    @media (max-width: $mobile) {
      margin-bottom: 20px;
    }
  }

  &__content {
    display: flex;
    flex-wrap: wrap;
    margin: 0 -12px -24px;
    @media (max-width: $tablet) {
      margin: 0 -8px -16px;
    }
  }

  &__item {
    width: calc((100% - 72px) / 3);
    margin: 0 12px 24px;
    height: 10vw;
    min-height: 200px;
    @media (max-width: $tablet) {
      width: calc((100% - 32px) / 2);
      margin: 0 8px 16px;
      height: 21vw;
      min-height: 158px;
    }

  }
}

.catalog-section-link {
  position: relative;
  overflow: hidden;
  padding: 36px;
  border-radius: 15px;
  @media (max-width: $tablet) {
    padding: 24px;
  }
  @media (max-width: $mobile) {
    padding: 16px 12px;
  }

  &__text {
    position: relative;
    z-index: 1;
    font-size: 22px;
    font-weight: 600;
    line-height: 24px;
    @media (max-width: $tablet) {
      font-size: 18px;
      line-height: 21.6px;
    }
  }

  &__picture {
    position: absolute;
    right: 0;
    bottom: 0;
    top: -20px;
    @media (max-width: $mobile) {
      top: 50px;
    }

    img {
      object-fit: contain;
      object-position: bottom right;
      width: 100%;
      height: 100%;
    }
  }
}
