.profile-menu {
  display: none;
  @media (max-width: $tablet) {
    display: block;
  }
  padding: 16px 70px;
  margin: 40px 0;
  background-color: #fff;
  box-shadow: 2px 2px 12.3px 0px #DFDCDC54;
  border-radius: 15px;

  @media (max-width: $mobile) {
    padding: 16px 30px;
    margin: 40px 0 20px;
  }
  @media (max-width: $mobileMedium) {
    padding: 10px 20px;
    margin: 40px 0 15px;
  }

  &__list {
    display: flex;
    justify-content: space-between;
  }

  &__item {
    display: flex;
    flex-direction: column;
    align-items: center;
    @include hover-effect;

    span {
      font-size: 14px;
      font-weight: 500;
      line-height: 19.1px;
      color: #6B7280;
      @media (max-width: $mobileMedium) {
        font-size: 12px;
        line-height: 16.37px;
      }
    }
  }

  &__icon {
    display: flex;
    margin-bottom: 6px;
    @media (max-width: $mobileMedium) {
      margin-bottom: 2px;
    }
  }
}