h1, .h1 {
  font-size: 40px;
  font-weight: 700;
  line-height: 48px;
  @media (max-width: $tablet) {
    font-size: 28px;
    line-height: 34px;
  }
  @media (max-width: $mobile) {
    font-size: 24px;
    line-height: 30px;
  }

}

h2, .h2 {
  font-size: 32px;
  font-weight: 700;
  line-height: 38.4px;
  margin-bottom: 24px;
  @media (max-width: $tablet) {
    font-size: 28px;
    font-weight: 700;
    line-height: 33.6px;
    margin-bottom: 20px;
  }
  @media (max-width: $mobile) {
    font-size: 24px;
    line-height: 28.8px;
    margin-bottom: 12px;
  }
}


h3, .h3 {
  font-size: 28px;
  font-weight: 700;
  line-height: 33.6px;
  @media (max-width: $tablet) {
  }
  @media (max-width: $mobile) {
  }
}

h4, .h4 {
  font-size: 24px;
  font-weight: 700;
  line-height: 28.8px;
  @media (max-width: $tablet) {
    font-size: 22px;
    line-height: 26px;
  }
  @media (max-width: $mobile) {
    font-size: 20px;
    line-height: 22px;
  }
}

h5, .h5 {
  font-size: 20px;
  font-weight: 600;
  line-height: 24px;
  @media (max-width: $tablet) {
  }
  @media (max-width: $mobile) {
  }
}

h6, .h6 {
  font-size: 18px;
  font-weight: 500;
  line-height: 21.6px;
  @media (max-width: $tablet) {
  }
  @media (max-width: $mobile) {
  }
}

.text-xl {
  font-size: 18px;
  line-height: 150%;
}

.text-l {
  font-size: 16px;
  line-height: 150%;
}

.text-m {
  font-size: 14px;
  line-height: 150%;
}

.text-s {
  font-size: 12px;
  line-height: 150%;
}

ol,
ul {
  list-style: none;
}

ol {
  counter-reset: ollist;
  list-style: none;

  li {
    margin-bottom: .5em;

    &::before {
      content: counter(ollist) ".";
      counter-increment: ollist;
      margin-right: 8px;
    }

  }
}


.text {
  color: #6B7280;
}


.text-l {
  font-size: 16px;
  font-weight: 500;
  line-height: 24px;
  color: #6B7280;
  @media (max-width: $mobile){
    font-size: 14px;
    line-height: 20px;
  }
}