@import "fonts";
@import "null";
@import "mixins";
@import "./vendors/swiper";


$blue: #1EB2E8;
$birch: #E8F9FF;
$black: #283238;
$grey: #6B7280;

$laptop: 1240px;
$tablet: 991.92px;
$mobile: 767.92px;
$mobileMedium: 600px;
@import "forms";
@import "typegr";
@import "btns";
@import "modal";
@import "./layout/header";
@import "./layout/footer";


body.blocked {
  overflow: hidden;
}

body.overlayed {
  position: relative;
  overflow: hidden;

  &::before {
    content: '';
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: #000;
    opacity: .2;
    z-index: 5;
    cursor: pointer;
  }
}

body.lock {
  overflow: hidden;
}

.container {
  box-sizing: border-box;
  margin-left: auto;
  margin-right: auto;
  max-width: 1800px;
  @media (max-width: 1920px) {
    max-width: 100%;
    padding: 0 60px;
  }
  @media (max-width: 1072px) {
    max-width: 952px;
    padding: 0;
  }
  @media (max-width: 991.92px) {
    max-width: 100%;
    padding: 0 20px;
  }
  @media (max-width: $mobileMedium) {
    padding: 0 15px;
  }
}

body {
  font-family: "Nunito", sans-serif;
  color: $black;
  font-size: 18px;
  line-height: 150%;
  height: 100%;
  display: flex;
  flex-direction: column;
}

main {
  -webkit-box-flex: 1;
  -ms-flex: 1 1 auto;
  flex: 1 1 auto;
}

.welcome {
  position: relative;
  margin-top: -60px;
  padding-top: 60px;

  &__wrapper {
    padding-bottom: 120px;
    @media (max-width: $laptop) {
      padding-bottom: 80px;
    }
    @media (max-width: $tablet) {
      padding: 87px 0 106px;
    }
    @media (max-width: $mobileMedium) {
      padding: 50px 0 100px;
    }
  }

  &__small-banner,
  &__big-banner {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  &__small-banner {
    display: none;
    @media (max-width: $tablet) {
      display: block;
    }
  }

  &__big-banner {
    display: block;
    @media (max-width: $tablet) {
      display: none;
    }
  }

  &__title {
    font-size: 48px;
    font-weight: 700;
    line-height: 57.6px;
    margin-bottom: 24px;
    @media (max-width: $tablet) {
      font-size: 40px;
      line-height: 48px;
    }
    @media (max-width: $mobileMedium) {
      font-size: 25px;
      line-height: 30px;
      margin-bottom: 16px;
    }
  }

  &__subtitle {
    max-width: 750px;
    font-size: 20px;
    font-weight: 600;
    line-height: 30px;
    margin-bottom: 40px;
    @media (max-width: $mobileMedium) {
      font-size: 18px;
      font-weight: 500;
      line-height: 21.6px;
      margin-bottom: 24px;
    }
  }

  &__slide {
    display: flex;
    align-items: center;
    color: #fff;
    @media (max-width: $tablet) {
      flex-direction: column;
      flex-wrap: wrap;
    }
  }

  &__info {
    width: 50%;
    flex-shrink: 0;
    margin-right: 68px;
    @media (max-width: $tablet) {
      width: 100%;
      text-align: center;
      margin-right: 0;
    }
  }

  &__picture {
    @media (max-width: $tablet) {
      padding: 10px 70px 0;
    }

    img {
      max-width: 100%;
      height: auto;
      object-fit: contain;
    }
  }

  &__wrapper {
    position: relative;
  }

  &__navigation {
    position: absolute;
    z-index: 1;
    display: flex;
    bottom: 85px;
    rotate: 0;
    right: 200px;
    @media (max-width: $laptop) {
      right: 0;
    }
    @media (max-width: $tablet) {
      left: 50%;
      transform: translateX(-50%);
      display: flex;
      justify-content: center;
      bottom: 50px;
    }
    @media (max-width: $mobileMedium) {
      bottom: 50px;
    }
  }
}

section {
  margin: 54px 0 100px;
  @media (max-width: $tablet) {
    margin: 74px 0;
  }
  @media (max-width: $mobile) {
    margin: 50px 0;
  }
}

.features {

  &__wrapper {
    display: flex;
    margin: 0 -2.5%;
    @media (max-width: $tablet) {
      flex-wrap: wrap;
      margin: 0 -2.5% -40px;
    }
    @media (max-width: $mobile) {
      margin: 0 -2.5% -24px;
    }
  }

  .feature-item {
    width: calc((75% / 5));
    flex-grow: 1;
    margin: 0 2.5%;
    @media (max-width: $tablet) {
      width: calc((85% / 3));
      margin: 0 2.5% 40px;
      flex-grow: 0;
    }
    @media (max-width: $mobile) {
      width: calc((90% / 2));
    }
  }
}

.feature-item {
  &__icon {
    margin-bottom: 20px;
    width: 120px;
    height: 120px;
    @media (max-width: $tablet) {
      width: 80px;
      height: 80px;
    }
    @media (max-width: $mobile) {
      width: 60px;
      height: 60px;
    }

    svg {
      width: 100%;
      height: 100%;
    }
  }

  &__name {
    font-size: 23px;
    font-weight: 700;
    line-height: 28.8px;
    margin-bottom: 4px;
    @media (max-width: $tablet) {
      font-size: 20px;
      font-weight: 600;
      line-height: 24px;
    }
    @media (max-width: $mobile) {
      font-size: 18px;
      font-weight: 500;
      line-height: 21.6px;
    }
  }

  &__text {
    font-size: 16px;
    font-weight: 400;
    line-height: 21.76px;
    color: #6B7280;
    @media (max-width: $mobile) {
      font-size: 14px;
      line-height: 21px;
    }
  }
}

.section-title {
  margin-bottom: 40px;
  @media (max-width: $tablet) {
    font-size: 28px;
    font-weight: 700;
    line-height: 33.6px;
    margin-bottom: 24px;
  }
  @media (max-width: $mobile) {
    font-size: 24px;
    line-height: 28.8px;
    margin-bottom: 20px;
  }
}

.stocks {
  margin: 74px 0;
  @media (max-width: $mobile) {
    margin: 50px 0;
  }

  &__content {
    display: flex;
    margin: 0 -12px;
    @media (max-width: $laptop) {
      margin: 0 -15px -30px;
      flex-wrap: wrap;
    }
    @media (max-width: $tablet) {
      margin: 0 -8px -20px;
    }
    @media (max-width: 640px) {
      margin: 0 0 -12px;
    }

    .stock-banner {
      margin: 0 12px;
      flex-grow: 1;
      @media (max-width: $laptop) {
        width: calc(50% - 30px);
        margin: 0 15px 30px;
      }
      @media (max-width: $tablet) {
        width: calc(50% - 16px);
        margin: 0 8px 20px;
      }
      @media (max-width: 640px) {
        width: 100%;
        margin: 0 0 12px;
      }
    }
  }
}

.seasons-nav {
  &--lb {
    .seasons-nav__arrow {
      background-color: #fff;

      svg path {
        fill: #283238;
      }
    }
  }

  &__arrow {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 50px;
    height: 50px;
    border: 1px solid #FFFFFF;
    border-radius: 50%;
    cursor: pointer;
    z-index: 1;

    &.swiper-button-disabled {
      cursor: auto;
      opacity: .5;
    }

    & + .seasons-nav__arrow {
      margin-left: 12px;
    }
  }
}


//cards
.card-item {
  display: inline-flex;
  flex-direction: column;
  padding: 20px;
  border-radius: 15px;
  background: #F8F9F9;

  &--light {
    background: #fff;
  }

  @media (max-width: $tablet) {
    padding: 12px 16px 16px;
  }

  &__label {
    display: inline-block;
    position: absolute;
    background: #E3A241;
    color: #fff;
    padding: 8px 10px;
    border-radius: 25px;
    font-size: 14px;
    font-weight: 600;
    line-height: 19.1px;
    letter-spacing: 0.008em;
    top: 3px;
    left: 0;
    @media (max-width: $mobile) {
      padding: 5px 8px;
      font-size: 12px;
      font-weight: 400;
      line-height: 18px;
    }
  }

  &__photo {
    position: relative;
    display: flex;
    justify-content: center;
    padding-top: 40px;
    margin-bottom: 17px;
    min-height: 352px;

    @media (max-width: $mobile) {
      padding-top: 8px;
    }

    img {
      max-height: 312px;
      max-width: 100%;
      object-fit: contain;
    }
  }

  &__subinfo {
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-size: 16px;
    font-weight: 400;
    line-height: 24px;
    color: #6B7280;
    margin-bottom: 12px;
  }

  &__rating {
    display: flex;
    align-items: center;
  }

  &__title {
    display: inline-block;
    font-size: 24px;
    font-weight: 700;
    line-height: 28.8px;
    margin-bottom: 4px;
    @media (max-width: $tablet) {
      font-size: 20px;
      font-weight: 600;
      line-height: 24px;
    }
  }

  &__description {
    font-size: 16px;
    font-weight: 500;
    line-height: 24px;
    margin-bottom: 20px;
    color: #6B7280;
    @media (max-width: $tablet) {
      margin-bottom: 16px;
    }
  }

  &__price {
    font-size: 20px;
    font-weight: 700;
    line-height: 26px;
    letter-spacing: 0.008em;
    margin-bottom: 24px;
    @media (max-width: $tablet) {
      font-size: 18px;
      line-height: 23.4px;
      margin-bottom: 20px;
    }

    span {
      font-size: 28px;
      line-height: 36.4px;
      letter-spacing: 0.008em;
    }
  }

  &__add-btn {
    display: flex;
    justify-content: center;
    width: 100%;
    text-align: center;
  }

  &__info {
    display: flex;
    flex-direction: column;
    flex-grow: 1;
  }

  .amount-logic {
    margin-bottom: 8px;
  }

  &__buttons {
    margin-top: auto;
  }
}

.card-rating {
  &__stars {
    display: flex;

    & + .card-rating__star {
      margin-left: 5px;
    }

    &__quant {
      margin-left: 3px;
      margin-bottom: -2px;
    }
  }

  &__star {
    display: flex;
  }


}

.amount-logic {
  display: flex;
  width: 100%;
  border: 1px solid #E9ECF2;
  border-radius: 15px;
  padding: 5px 8px;

  &__more {
    display: inline-block;
    width: 40px;
    height: 40px;
    margin-left: auto;
    background-color: #EDEDED;
    border-radius: 12px;
  }

  &__less {
    display: inline-block;
    width: 40px;
    height: 40px;
    background-color: #EDEDED;
    margin-right: auto;
    border-radius: 12px;
  }

  &__info {
    display: flex;
    flex-direction: column;
    align-items: center;
    white-space: nowrap;

    span {
      display: block;
    }

    span:nth-child(1) {
      font-size: 16px;
      font-weight: 500;
      line-height: 21px;
      margin-bottom: 4px;
    }

    span:nth-child(2) {
      font-size: 14px;
      font-weight: 400;
      line-height: 14px;
      color: #6B7280;
    }
  }
}

.section-title-block {
  display: flex;
  justify-content: space-between;
  align-items: center;

  .btn-underline {
    @media (max-width: $tablet) {
      font-size: 18px;
      font-weight: 500;
      line-height: 27px;
    }
    @media (max-width: $mobileMedium) {
      display: none;
    }
  }
}


//popular
.popular {
  &__content {
    @media (max-width: $tablet) {
      margin-right: -20px;
    }
    @media (max-width: $mobileMedium) {
      margin-right: -15px;
    }
  }

  &__content {
    @media (max-width: $mobile) {
      .swiper-slide {
        width: 75vw;
      }
    }

    .card-item {
      flex-grow: 1;
      max-width: unset;
      @media (max-width: $laptop) {
        margin: 8px;
      }
    }
  }
}

.catalog-preview {
  &__content {
    background: #F6F7F8;
    border-radius: 15px;
    padding: 30px 30px 0;
    @media (max-width: $tablet) {
      padding: 20px 16px;
    }
  }

  &__tabs {
    display: flex;
    overflow-x: auto;
    margin-bottom: 30px;
  }

  &__box {
    display: none;

    &[data-active] {
      display: flex;
    }
  }

  &__tab {
    display: inline-block;
    font-size: 16px;
    font-weight: 500;
    line-height: 20.8px;
    letter-spacing: 0.008em;
    padding: 14px 12px;
    border: 1px solid #E3E6E9;
    border-radius: 10px;
    cursor: pointer;
    white-space: nowrap;
    @include hover-effect;

    &[data-active] {
      background: #1EB2E8;
      border: 1px solid #1EB2E8;
      color: #fff;
      cursor: default;

      &:hover {
        opacity: 1;
      }
    }

    & + .catalog-preview__tab {
      margin-left: 12px;
    }
  }
}

.section-products {
  @media (max-width: $tablet) {
    flex-direction: column;
  }

  &__title {
    font-size: 30px;
    font-weight: 700;
    line-height: 36px;
    margin-bottom: 16px;
    @media (max-width: $tablet) {
      font-size: 24px;
      font-weight: 700;
      line-height: 28.8px;
      margin-bottom: 12px;
    }
    @media (max-width: $mobile) {
      font-size: 20px;
      font-weight: 600;
      line-height: 24px;
    }
  }

  &__description {
    margin-bottom: 36px;
    color: #6B7280;
    @media (max-width: $mobile) {
      margin-bottom: 20px;
    }
  }

  &__info {
    display: flex;
    flex-direction: column;
    margin-right: 7%;
    margin-bottom: 15px;
    @media (max-width: 1440px) {
      margin-right: 5%;
    }
    @media (max-width: $tablet) {
      margin-right: 0;
      margin-bottom: 24px;
    }
  }

  &__photo {
    flex-grow: 1;
    @media (max-width: $tablet) {
      display: none;
    }

    img {
      width: 100%;
      height: 100%;
      object-fit: contain;
      object-position: bottom;
    }
  }

  &__items {
    position: relative;
    width: 55%;
    flex-shrink: 0;
    padding-bottom: 60px;
    padding-left: 70px;
    padding-right: 70px;
    @media (max-width: 1440px) {
      width: 57%;
      padding-left: 60px;
      padding-right: 60px;
      margin-right: -20px;
    }
    @media (max-width: $tablet) {
      width: unset;
      padding-right: 0;
      padding-left: 0;
      padding-bottom: 0;
      margin-right: -15px;
    }

    .swiper-slide {
      width: 400px;

      @media (max-width: $tablet) {
        width: 40vw;
      }
      @media (max-width: $mobile) {
        width: 75vw;
      }
    }

    .seasons-nav__arrow {
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      @media (max-width: $tablet) {
        display: none;
      }

      &--right {
        right: 0;
      }

      &--left {
        left: 0;
      }
    }
  }
}


//text-and-picture
.text-and-picture {
  &__wrapper {
    display: flex;
    align-items: center;
    @media (max-width: $tablet) {
      flex-direction: column;
    }
  }

  &__media {
    width: 49%;
    flex-shrink: 0;
    margin-left: 6%;
    @media (max-width: $tablet) {
      width: 100%;
      margin-left: 0;
      margin-top: 32px;
    }
    @media (max-width: $mobile) {
      margin-top: 24px;
    }

    img,
    video {
      max-width: 100%;
      height: auto;
    }
  }

  &__pre-icon {
    display: flex;
    margin-bottom: 34px;
    @media (max-width: $tablet) {
      margin-bottom: 26px;
    }
    @media (max-width: $mobile) {
      max-width: 60px;
      margin-bottom: 20px;
    }

    svg, img {
      max-width: 100%;
    }
  }

  &__title {
    font-size: 32px;
    font-weight: 700;
    line-height: 38.4px;
    margin-bottom: 20px;
    @media (max-width: $tablet) {
      font-size: 28px;
      line-height: 32px;
      margin-bottom: 18px;
    }
    @media (max-width: $mobile) {
      font-size: 24px;
      line-height: 28.8px;
      margin-bottom: 16px;
    }
  }

  &__text {
    font-size: 16px;
    font-weight: 400;
    line-height: 25.6px;
    margin-bottom: 36px;
    color: #6B7280;
    @media (max-width: $tablet) {
      margin-bottom: 24px;
    }
    @media (max-width: $mobile) {
      margin-bottom: 20px;
    }
  }
}


.privacy {
  @media (max-width: $mobile) {
    font-size: 16px;
    line-height: 20px;
  }

  h1 {
    margin-bottom: 12px;
  }

  h1, h2, h3, h4, h5 {
    margin-bottom: .5em;
  }

  p {
    margin-bottom: .4em;
  }

}


@import "components/breadcrumbs";
@import "components/catalog";
@import "components/catalog-card";
@import "components/error";
@import "components/lk";
@import "components/profile-menu";
@import "components/search";
@import "components/auth-pages";


.current-status-page {
  &__wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100%;
    text-align: center;
    min-height: 40vh;
    margin: auto;
  }

  &--search {
    .current-status-page__wrapper {
      max-width: 528px;
    }

    .current-status-page__icon {
      @media (max-width: $mobile) {
        svg {
          width: 100px;
          height: 100px;
        }
      }
    }
  }

  &__icon {
    display: flex;
    margin-bottom: 24px;
    @media (max-width: $mobile) {
      margin-bottom: 16px;
      svg {
        width: 40px;
        height: 40px;
      }
    }
  }

  &__title {
    margin-bottom: 8px;
    @media (max-width: $tablet) {
      font-size: 28px;
      font-weight: 700;
      line-height: 33.6px;
    }
    @media (max-width: $mobile) {
      font-size: 24px;
      line-height: 28.8px;
    }
  }

  &__subtitle {
    color: #6B7280;
    margin-bottom: 32px;
    @media (max-width: $tablet) {
      font-size: 16px;
      font-weight: 500;
      line-height: 24px;
    }
    @media (max-width: $mobile) {
      margin-bottom: 28px;
    }
  }
}

.basket {
  margin-top: 24px;

  &__empty {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    margin-top: 75px;
    @media (max-width: $mobile) {
      margin-top: 32px;
    }
  }

  &__subtitle {
    font-size: 32px;
    font-weight: 700;
    line-height: 38.4px;
    margin-bottom: 16px;
    @media (max-width: $tablet) {
      font-size: 28px;
      line-height: 33.6px;
    }
    @media (max-width: $mobile) {
      font-size: 20px;
      font-weight: 600;
      line-height: 24px;
    }
  }

  &__icon {
    display: flex;
    margin-bottom: 20px;
    @media (max-width: $tablet) {
      padding: 0 70px;
    }

    svg,
    img {
      max-width: 100%;
    }
  }

  &__text {
    color: #6B7280;
    @media (max-width: $tablet) {
      font-size: 16px;
      font-weight: 500;
      line-height: 24px;
    }
  }
}


.basket-amount {
  display: inline-flex;
  border: 1px solid #E9ECF2;
  background: #F6F7F880;
  border-radius: 12px;
  padding: 6px;

  &__less {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-shrink: 0;
    width: 20px;
    height: 20px;
    cursor: pointer;
    @include hover-effect;

    &::before {
      content: '';
      display: inline-flex;
      width: 15px;
      height: 2px;
      background-image: url("data:image/svg+xml;charset=UTF-8,%3csvg width='15' height='2' viewBox='0 0 15 2' fill='none' xmlns='http://www.w3.org/2000/svg'%3e%3cpath d='M1.33203 1H13.7617H1.33203Z' fill='%236B7280'/%3e%3cpath d='M1.33203 1H13.7617' stroke='%236B7280' stroke-width='1.5' stroke-linecap='round' stroke-linejoin='round'/%3e%3c/svg%3e ");
    }
  }

  &__more {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-shrink: 0;
    width: 20px;
    height: 20px;
    cursor: pointer;
    @include hover-effect;

    &::before {
      content: '';
      width: 14px;
      height: 14px;
      display: inline-flex;
