@import "components/basket";

.contacts {
  &__wrapper {

  }

  &__title {
    margin-bottom: 50px;
    @media (max-width: $tablet) {
      margin-bottom: 30px;
    }
  }

  &__tabs {
    display: flex;
    overflow: auto;
    margin: -6px;
    margin-bottom: 44px;
    @media (max-width: $tablet) {
      margin-bottom: 33px;
    }

    .contacts-tab {
      margin: 6px;
    }
  }


  &__subtitle {
    font-size: 28px;
    font-weight: 700;
    line-height: 33.6px;
    margin-bottom: 32px;
    @media (max-width: $tablet) {
      font-size: 24px;
      line-height: 28.6px;
      margin-bottom: 24px;
    }
    @media (max-width: $mobile) {
      font-size: 20px;
      line-height: 24px;
      margin-bottom: 18px;
    }
  }

  &__common-info {
    position: relative;
    display: flex;
    padding-top: calc(50px - 3%);
    margin: -3%;
    margin-top: 50px;

    @media (max-width: $tablet) {
      margin-top: 30px;
      padding-top: calc(30px - 3%);
    }
    @media (max-width: $mobile) {
      flex-wrap: wrap;
      margin-top: 30px;
      padding-top: 30px;
    }

    &::before {
      content: '';
      position: absolute;
      width: 94%;
      height: 1px;
      left: 3%;
      top: 0;
      border-top: 1px solid #9C9D9F;
      @media (max-width: $mobile) {
        width: 100%;
        left: 0;
      }
    }

    & > div {
      margin: 3%;
      max-width: 46%;
      @media (max-width: $mobile) {
        max-width: unset;
        width: 100%;
        margin: 0 0 20px;
      }
    }
  }
}

.contacts-block {
  display: none;

  &[data-active] {
    display: block;
  }

  &__info {
    display: flex;
    flex-wrap: wrap;
    margin: -3%;
    @media (max-width: $mobile) {
      margin: 0 0 -20px;
    }
  }

  &__info-item {
    margin: 3%;
    max-width: 44%;
    @media (max-width: $mobile) {
      max-width: unset;
      width: 100%;
      margin: 0 0 20px;
    }
  }

  &__map {
    margin-top: 32px;
    border-radius: 12px;
    overflow: hidden;
    @media (max-width: $tablet) {
      margin-top: 24px;
    }
  }
}

.contacts-tab {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  padding: 12px;
  font-size: 16px;
  font-weight: 500;
  line-height: 20.8px;
  letter-spacing: 0.008em;
  box-shadow: 4px 4px 11.1px 0px #CDCCCC2E;
  border: 1px solid #E3E6E9;
  border-radius: 10px;
  cursor: pointer;
  @media (max-width: $mobile) {
    padding: 8px;
    font-size: 14px;
    line-height: 16.8px;
  }

  &[data-active] {
    background-color: #1EB2E8;
    color: #fff;
  }
}

.contact-info-item {
  @media (max-width: $mobile) {
    font-size: 16px;
    line-height: 20px;
  }

  &__title {
    font-size: 20px;
    font-weight: 600;
    line-height: 24px;
    margin-bottom: 12px;
    @media (max-width: $tablet) {
      font-size: 18px;
      line-height: 22px;
      margin-bottom: 10px;
    }
    @media (max-width: $mobile) {
      font-size: 16px;
      line-height: 20px;
    }
  }

  p {
    margin-bottom: .3em;
    @media (max-width: $tablet) {
      font-size: 16px;
      line-height: 20px;
    }
  }

  &__thin-text {
    font-size: 16px;
    font-weight: 500;
    line-height: 24px;
    color: #6B7280;
    @media (max-width: $tablet) {
      font-size: 14px;
      line-height: 20px;
    }
  }
}

.contacts-requisites {
  &__title {
    font-size: 28px;
    font-weight: 700;
    line-height: 33.6px;
    margin-bottom: 16px;
  }

  &__item {
    margin-bottom: 8px;

    span {
      &:nth-child(1) {
        font-weight: 500;
        color: #6B7280;
        margin-right: 4px;
        @media (max-width: $tablet) {
          font-size: 16px;
          line-height: 24px;
        }
      }

      &:nth-child(2) {
        @media (max-width: $tablet) {
          font-size: 16px;
          line-height: 24px;
        }
      }
    }
  }
}

.contacts-files {
  &__link {
    margin-bottom: 16px;
  }
}

.info-blocks {
  &__title {
    margin-bottom: 50px;
    @media (max-width: $tablet) {
      margin-bottom: 35px;
    }
    @media (max-width: $mobile) {
      margin-bottom: 20px;
    }
  }

  &__wrapper {
    display: flex;
    margin: -8px;
    @media (max-width: $tablet) {
      flex-wrap: wrap;
    }
    @media (max-width: $mobile) {
      margin: 0 0 -12px;
    }

    .text-icon-block {
      flex-grow: 1;
      margin: 8px;
      width: calc(50% - 16px);
      @media (max-width: $mobile) {
        width: 100%;
        margin: 0 0 12px;
      }
    }
  }

  &__subtitle {
    font-size: 24px;
    font-weight: 700;
    line-height: 28.8px;
    margin-bottom: 32px;
    @media (max-width: $tablet) {
      font-size: 20px;
      line-height: 24px;
      margin-bottom: 24px;
    }
    @media (max-width: $mobile) {
      margin-bottom: 16px;
    }
  }

  &__payments {
    padding-top: 50px;
    margin-top: 50px;
    border-top: 1px solid #BBBBBB;
    @media (max-width: $mobile) {
      padding-top: 30px;
      margin-top: 30px;
    }
  }
}

.text-icon-block {
  border: 1px solid #E9ECF2;
  padding: 20px;
  border-radius: 15px;
  @media (max-width: $mobile) {
    padding: 12px;
  }

  &__icon {
    display: flex;
    width: 40px;
    margin-bottom: 12px;


    svg,
    img {
      max-width: 100%;
    }
  }

  &__title {
    font-size: 20px;
    font-weight: 600;
    line-height: 24px;
    margin-bottom: 8px;
    @media (max-width: $mobile) {
      font-size: 18px;
      line-height: 22px;
    }
  }

  &__text {
    font-size: 16px;
    font-weight: 500;
    line-height: 24px;
    color: #6B7280;
    @media (max-width: $mobile) {
      font-size: 14px;
      line-height: 18px;
    }
  }
}

.welcome-picture {
  &__title-block {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    margin-bottom: 40px;
    @media (max-width: $laptop) {
      flex-wrap: wrap;
    }
    @media (max-width: $mobile) {
      margin-bottom: 20px;
    }
  }

  &__text {
    max-width: 550px;
    @media (max-width: $laptop) {
      max-width: unset;
      width: 100%;
      margin-top: 20px;
    }
    @media (max-width: $mobile) {
      font-size: 16px;
      line-height: 20px;
    }
  }

  &__title {
    max-width: 800px;
    margin-right: 40px;
    @media (max-width: $laptop) {
      max-width: 100%;
      width: 100%;
    }
  }

  &__picture {
    border-radius: 15px;
    overflow: hidden;

    img {
      width: 100%;
      height: auto;
      object-fit: cover;
    }
  }
}

.values {
  &__content {
    display: flex;
    flex-wrap: wrap;
    margin: -8px;
    @media (max-width: $mobile) {
      margin: 0 0 -12px;
    }

    .value-item {
      width: calc(33% - 16px);
      margin: 8px;
      height: auto;
      @media (max-width: $tablet) {
        width: calc(50% - 16px);
      }
      @media (max-width: $mobile) {
        width: 100%;
        margin: 0 0 12px;
      }
    }
  }
}

.value-item {
  border-radius: 15px;
  overflow: hidden;
  padding: 50px 24px 24px;
  background: #F8F9F9;
  @media (max-width: $tablet) {
    padding: 40px 16px 16px;
  }
  @media (max-width: $mobile) {
    padding: 24px 12px 12px;
  }

  &--picture {
    padding: 0;
    @media (max-width: $mobile) {
      max-height: 35vw;
    }

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  &__title {
    font-size: 28px;
    font-weight: 700;
    line-height: 33.6px;
    margin-bottom: 24px;
    @media (max-width: $tablet) {
      font-size: 24px;
      line-height: 28.6px;
      margin-bottom: 18px;
    }
    @media (max-width: $mobile) {
      font-size: 20px;
      line-height: 24.6px;
      margin-bottom: 14px;
    }
  }

  &__text {
    font-size: 18px;
    font-weight: 400;
    line-height: 27px;
    color: #6B7280;
    @media (max-width: $mobile) {
      font-size: 16px;
      line-height: 24px;
    }
  }
}

.three-news {
  &__title {
    margin-bottom: 32px;
    @media (max-width: $tablet) {
      margin-bottom: 24px;
    }
  }

  &__wrapper {
    display: flex;
    margin: -8px;
    @media (max-width: $tablet) {
      flex-wrap: wrap;
      margin: 0;
    }

    .three-news-item {
      width: calc(33% - 16px);
      margin: 8px;
      @media (max-width: $tablet) {
        width: calc(50% - 16px);
        margin: 0 8px 36px;
        flex-grow: 1;
      }
      @media (max-width: $mobile) {
        width: 100%;
        margin: 0 0 24px;
      }
    }
  }
}

.three-news-item {
  &__picture {
    margin-bottom: 25px;
    overflow: hidden;
    border-radius: 15px;
    height: 26vw;
    @media (max-width: $tablet) {
      margin-bottom: 18px;
    }
    @media (max-width: $mobile) {
      height: 40vw;
    }

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  &__title {
    font-size: 28px;
    font-weight: 700;
    line-height: 33.6px;
    margin-bottom: 16px;
    @media (max-width: $tablet) {
      font-size: 24px;
      line-height: 28px;
      margin-bottom: 12px;
    }
    @media (max-width: $mobile) {
      font-size: 20px;
      line-height: 24px;
      margin-bottom: 8px;
    }
  }

  &__text {
    font-size: 18px;
    font-weight: 400;
    line-height: 27px;
    color: #6B7280;
    @media (max-width: $tablet) {
      font-size: 16px;
      line-height: 24px;
    }
  }
}

.four-news {
  &__title {
    margin-bottom: 32px;
    @media (max-width: $tablet) {
      margin-bottom: 24px;
    }
  }

  &__content {
    display: flex;
    flex-wrap: wrap;
    margin: -8px;
    @media (max-width: $mobile) {
      width: 100%;
      margin: 0 0 -12px;
    }

    .four-news-item {
      width: calc(66% - 16px);
      margin: 8px;
      @media (max-width: $tablet) {
        width: calc(50% - 16px);
      }
      @media (max-width: $mobile) {
        width: 100%;
        margin: 0 0 12px;
      }

      &:nth-child(4n - 1),
      &:nth-child(4n - 2) {
        width: calc(33% - 16px);
        @media (max-width: $tablet) {
          width: calc(50% - 16px);
        }
        @media (max-width: $mobile) {
          width: 100%;
        }
      }
    }
  }
}

.four-news-item {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 26vw;
  border-radius: 15px;
  overflow: hidden;
  @media (max-width: $tablet) {
    min-height: 50vw;
  }

  &__picture {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: -1;

    &::before {
      content: '';
      position: absolute;
      opacity: .2;
      background-color: #000;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
    }

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  &__info {
    margin-top: auto;
    padding: 32px 24px;
    background: linear-gradient(360deg, rgba(0, 0, 0, 0.5) 3.68%, rgba(0, 0, 0, 0) 100%);
    backdrop-filter: blur(2px);
    @media (max-width: $laptop) {
      padding: 24px 18px;
    }
    @media (max-width: $tablet) {
      padding: 20px 16px;
    }
  }

  &__title {
    color: #fff;
    font-size: 28px;
    font-weight: 700;
    line-height: 33.6px;
    margin-bottom: 16px;
    @media (max-width: $laptop) {
      font-size: 24px;
      line-height: 28px;
      margin-bottom: 12px;
    }
    @media (max-width: $tablet) {
      font-size: 22px;
      line-height: 26px;
    }
    @media (max-width: $mobile) {
      font-size: 20px;
      line-height: 24px;
    }
  }

  &__text {
    color: #fff;
    @media (max-width: $mobile) {
      font-size: 16px;
      line-height: 20px;
    }
  }
}

.about-banner {
  &__wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 60px 0 80px;
    background-color: #f8f9f9;
    background-repeat: no-repeat;
    border-radius: 15px;
    overflow: hidden;
    background-size: 100%;
    background-position: center;
    @media (max-width: $tablet) {
      padding: 40px 0 60px;
    }
    @media (max-width: $mobile) {
      padding: 40px 0;
    }
  }

  &__text {
    font-size: 40px;
    font-weight: 600;
    line-height: 53.2px;
    text-align: center;
    max-width: 1160px;
    margin-bottom: 40px;
    @media (max-width: $tablet) {
      font-size: 32px;
      line-height: 40.2px;
      margin-bottom: 30px;
    }
    @media (max-width: $mobile) {
      font-size: 26px;
      line-height: 30.2px;
      margin-bottom: 20px;
    }
    @media (max-width: $mobileMedium) {
      font-size: 22px;
      line-height: 26px;
    }
  }

  &__years {
    font-size: 96px;
    font-weight: 600;
    line-height: 127.68px;
    @media (max-width: $tablet) {
      font-size: 72px;
      line-height: 92.68px;
    }
    @media (max-width: $mobile) {
      font-size: 48px;
      line-height: 60.68px;
    }
    @media (max-width: $mobileMedium) {
