}

.about-label {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 8px 20px;
  white-space: nowrap;
  font-size: 20px;
  font-weight: 600;
  line-height: 24px;
  text-align: center;
  border-radius: 30px;
  color: #fff;
  flex-shrink: 0;
  @media (max-width: $tablet) {
    font-size: 18px;
    line-height: 22px;
  }
  @media (max-width: $mobile) {
    font-size: 16px;
    line-height: 20px;
    height: 40px;
  }
  @media (max-width: $mobileMedium) {
    font-size: 14px;
    line-height: 18px;
    height: 36px;
  }

  &--pic {
    padding: 0;
    border-radius: 50%;
    width: 50px;
    height: 50px;
    @media (max-width: $mobile) {
      width: 40px;
      height: 40px;
    }
    @media (max-width: $mobileMedium) {
      height: 36px;
      width: 36px;
    }

    svg {
      max-width: 32px;
      @media (max-width: $mobile) {
        max-width: 26px;
      }
      @media (max-width: $mobileMedium) {
        max-width: 22px;
      }
    }
  }
}

