.form-input {
  position: relative;
  display: inline-block;
  width: 100%;

  &--search {

  }


  &__error-text {
    position: absolute;
    z-index: 1;
    top: calc(100% + 4px);
    left: 0;
    font-size: 14px;
    color: #FF5252;
    line-height: 21px;
  }

  &:not(.form-input--error) {
    input,
    textarea {
      &:hover,
      &:active {
        border: 1px solid #D8DBE2;
      }

      &[disabled]:hover {
        border: 1px solid #EFF2F9;
      }
    }
  }


  input,
  textarea {
    font-size: 16px;
    font-weight: 400;
    line-height: 24px;
    width: 100%;
    border: 1px solid #EFF2F9;
    padding: 13px 20px;
    border-radius: 15px;
    background-color: #F6F7F8;
    color: #3A3A3A;


    &::placeholder {
      color: #8F8F8F;
    }
  }

  textarea {
    resize: none;
  }

  &--m {
    input,
    textarea {
      font-size: 14px;
      font-weight: 400;
      line-height: 21px;
      padding: 9.5px 15px;
    }
  }

  &:not(.form-input--error)[data-focused] {
    &::after {
      content: '';
      position: absolute;
      right: -2px;
      top: -2px;
      bottom: -2px;
      left: -2px;
      border: 1px solid #D8DBE2;
      border-radius: 15px;
    }

    input,
    textarea {
      border: 1px solid #EFF2F9;
    }
  }

  input[type='radio'] {
    display: none;

    & + label {
      position: relative;
      display: flex;
      align-items: center;
      padding-left: 35px;
      cursor: pointer;

      @media (max-width: $mobile) {
        font-size: 16px;
        line-height: 20px;
      }

      &::before {
        content: '';
        position: absolute;
        left: 0;
        top: 50%;
        transform: translateY(-50%);
        width: 20px;
        height: 20px;
        border: 1px solid #101010;
        border-radius: 50%;
        margin-right: 15px;
      }
    }

    &:checked + label {
      &::before {
        background-color: #1EB2E8;
        border-color: #1EB2E8;
      }

      &::after {
        content: '';
        position: absolute;
        top: 50%;
        left: 5px;
        width: 10px;
        transform: translateY(-50%);
        height: 10px;
        background-color: #fff;
        border-radius: 50%;
        margin-right: 15px;
      }
    }
  }

  input[type='checkbox'] {
    display: none;

    & + label {
      position: relative;
      display: flex;
      align-items: center;
      padding-left: 35px;
      cursor: pointer;
      color: #8F8F8F;
      font-size: 12px;
      font-weight: 400;
      line-height: 16.37px;
      letter-spacing: 0.05em;

      &::before {
        content: '';
        position: absolute;
        left: 0;
        top: 50%;
        transform: translateY(-50%);
        width: 20px;
        height: 20px;
        border: 1px solid #88929B;
        border-radius: 3px;
        margin-right: 15px;
      }
    }

    &:checked + label {
      &::after {
        content: '';
        width: 11px;
        height: 9px;
        display: inline-block;
        left: 5px;
        position: absolute;
        background-image: url("data:image/svg+xml;charset=UTF-8,%3csvg width=%2711%27 height=%279%27 viewBox=%270 0 11 9%27 fill=%27none%27 xmlns=%27http://www.w3.org/2000/svg%27%3e%3cpath d=%27M10.4065 0.261455C10.1866 0.0735871 9.90101 -0.0192547 9.61262 0.00333493C9.32423 0.0259246 9.05661 0.162097 8.86857 0.381924L4.1717 5.87497L1.6449 3.7656C1.54487 3.67955 1.42873 3.61424 1.30324 3.57347C1.17775 3.53271 1.0454 3.51729 0.913899 3.52812C0.782395 3.53895 0.654356 3.57581 0.537225 3.63657C0.420093 3.69732 0.316206 3.78074 0.231601 3.882C0.146997 3.98325 0.0833629 4.10032 0.044399 4.22638C0.00543504 4.35245 -0.00808177 4.485 0.00463411 4.61633C0.01735 4.74767 0.056045 4.87517 0.11847 4.99141C0.180895 5.10766 0.265804 5.21034 0.368262 5.29349L3.75357 8.12005C3.85796 8.20708 3.97915 8.27168 4.1096 8.30983C4.24004 8.34797 4.37695 8.35885 4.51178 8.34177C4.65477 8.33072 4.79418 8.29158 4.92203 8.22659C5.04988 8.1616 5.16366 8.07203 5.25686 7.96302L10.5263 1.79895C10.714 1.57888 10.8067 1.29334 10.784 1.00501C10.7614 0.716672 10.6253 0.449091 10.4056 0.260986%27 fill=%27white%27/%3e%3c/svg%3e");
      }

      &::before {
        background-color: #1eb2e8;
        border-color: #1eb2e8;
      }
    }
  }

  &--error {
    input[type='checkbox'] {
      & + label {
        color: #FF5252;

        &::before {
          border-color: #FF5252;
        }
      }
    }
  }

  &__search-submit {
    position: absolute;
    display: flex;
    padding: 10px;
    right: 10px;
    top: 50%;
    transform: translateY(-50%);
  }

  &--error {
    input,
    textarea {
      border-color: #FF5252;
    }
  }

  &--success {
    .form-input__error-text {
      display: none;
    }
  }

  label {
    margin-bottom: 6px;
  }
}


.form-body {
  &__fields-wrapper {
    background-color: #fff;
    padding: 30px;
    border-radius: 30px;
    @media (max-width: $tablet) {
      border-radius: 15px;
      padding: 16px;
    }
    @media (max-width: $mobile) {
      padding: 12px;
    }
  }

  &__field {
    label {
      display: block;
      font-size: 16px;
      font-weight: 400;
      line-height: 24px;
      margin-bottom: 6px;
    }
  }

  &__two-in-row {
    display: flex;
    margin: 0 -15px;
    @media (max-width: $tablet) {
      flex-wrap: wrap;
      margin: 0;
    }

    &:last-child {
      margin-bottom: -40px;
      @media (max-width: $tablet) {
        margin-bottom: -16px;
      }
    }

    .form-body__field {
      width: calc(50% - 30px);
      margin: 0 15px 40px;
      @media (max-width: $tablet) {
        width: 100%;
        margin: 0 0 16px;
      }
    }
  }

  &__buttons {
    display: flex;
    align-items: center;
    margin-top: 24px;

    .btn {
      & + .btn {
        margin-left: 12px;
      }
    }
  }

  &__results {
    display: none;
    position: absolute;
    margin-top: 12px;
    font-size: 18px;
    line-height: 24px;
    font-weight: 500;
    @media (max-width: $tablet) {
      margin-top: 8px;
      font-size: 14px;
      line-height: 16px;
    }
    @media (max-width: $mobile) {
      font-size: 12px;
      line-height: 14px;
    }

    &[data-success] {
      display: block;
      color: #1EB2E8;
    }

    &[data-error] {
      display: block;
      color: #FF5252;
    }
  }
}

.password-eye {
  position: absolute;
  right: 20px;
  top: 25px;
  transform: translateY(-50%);
  cursor: pointer;
  display: inline-block;
  width: 20px;
  height: 12px;
  background-image: url("data:image/svg+xml;charset=UTF-8,%3csvg width='20' height='12' viewBox='0 0 20 12' fill='none' xmlns='http://www.w3.org/2000/svg'%3e%3cpath d='M10 0C5.68182 0 1.99409 2.488 0.5 6C1.99409 9.512 5.68182 12 10 12C14.3182 12 18.0059 9.512 19.5 6C18.0059 2.488 14.3182 0 10 0ZM10 10C7.61636 10 5.68182 8.208 5.68182 6C5.68182 3.792 7.61636 2 10 2C12.3836 2 14.3182 3.792 14.3182 6C14.3182 8.208 12.3836 10 10 10ZM10 3.6C8.56636 3.6 7.40909 4.672 7.40909 6C7.40909 7.328 8.56636 8.4 10 8.4C11.4336 8.4 12.5909 7.328 12.5909 6C12.5909 4.672 11.4336 3.6 10 3.6Z' fill='%23E0E4EA'/%3e%3c/svg%3e ");
}

.form {
  .form__some {
    display: flex;
    margin-top: 16px;

    .form-input {
      margin: 0 6px 12px;

      & + .form-input {
        margin-top: 0;
      }
    }
  }
}

.form-select {
  .choices {
    &__inner {
      border: 1px solid #E9ECF2;
      border-radius: 15px;
      background: #fff;
      overflow: hidden;
      font-size: 16px;
      font-weight: 400;
      line-height: 24px;
    }

    &[data-type*="select-one"].is-open::after {
      border-color: transparent transparent #E9ECF2;
    }

    &[data-type*="select-one"] {
      border-radius: 15px;
    }

    .choices__list {
      .choices__item {
        font-size: 16px;
        font-weight: 400;
        line-height: 24px;

        &.is-selected {
          color: #1EB2E8;
        }
      }
    }

    &[data-type*="select-one"]::after {
      margin-top: 0;
      background-image: url("data:image/svg+xml;charset=UTF-8,%3csvg width='24' height='24' viewBox='0 0 24 24' fill='none' xmlns='http://www.w3.org/2000/svg'%3e%3cpath fill-rule='evenodd' clip-rule='evenodd' d='M7.48682 9.84205C7.85106 9.50383 8.42051 9.52492 8.75874 9.88916L11.9992 13.3789L15.2397 9.88916C15.5779 9.52492 16.1474 9.50383 16.5116 9.84205C16.8759 10.1803 16.897 10.7497 16.5587 11.114L12.6587 15.314C12.4884 15.4974 12.2495 15.6016 11.9992 15.6016C11.749 15.6016 11.51 15.4974 11.3397 15.314L7.43971 11.114C7.10149 10.7497 7.12258 10.1803 7.48682 9.84205Z' fill='%239CA5AD'/%3e%3c/svg%3e ");
      border: none;
      width: 24px;
      height: 24px;
      background-repeat: no-repeat;
      background-position: center;
      transform: translateY(-50%);
      transition: all .3s;
    }

    &[data-type*="select-one"].is-open::after {
      margin-top: 0;
      transform: translateY(-50%) rotate(180deg);
    }

    &__list--dropdown,
    &__list[aria-expanded] {
      border: 1px solid #E9ECF2;
      border-bottom-left-radius: 15px;
      border-bottom-right-radius: 15px;
    }

    &.is-open .choices__inner {
      border-radius: 15px 15px 0 0;
    }

    &.is-focused .choices__inner,
    &.is-open .choices__inner {
      border-color: #E9ECF2;
    }

    &.is-open .choices__list--dropdown,
    &.is-open .choices__list[aria-expanded] {
      border-color: #E9ECF2;
    }
  }
}

.form-radio-box {
  display: inline-block;
  cursor: pointer;

  input {
    display: none;

    &:checked + label {
      border: 1px solid #1EB2E8;
      background: #E8F9FF;
      color: #1EB2E8;

      &:hover {
        opacity: 1;
      }
    }
  }

  label {
    display: inline-block;
    padding: 10px 12px;
    background-color: #fff;
    cursor: pointer;
    border: 1px solid #E9ECF2;
    border-radius: 10px;
    font-size: 16px;
    font-weight: 500;
    line-height: 24px;
    color: #6B7280;
    @include hover-effect;
    @media (max-width: $mobile) {
      padding:8px 10px;
      font-size: 14px;
      line-height: 21px;
    }
  }
}