.footer {
  border-top: 1px solid #CCCCCA4D;
  border-radius: 50px 50px 0 0;
  background-color: #fff;

  &__top {
    padding: 60px 0 52px;
    @media (max-width: $tablet) {
      padding: 20px 0 35px;
    }
    @media (max-width: $mobile) {
      padding: 20px 0;
    }
  }


  &__nav {
    display: flex;
    justify-content: space-between;
    flex-grow: 1;
    @media (max-width: $mobile) {
      flex-direction: column;
    }
  }

  &__nav-content {
    display: flex;
    margin-right: 8%;
    @media (max-width: 1600px) {
      margin-right: 0;
    }
    @media (max-width: $laptop) {
      flex-wrap: wrap;
    }
  }

  &__logo {
    flex-shrink: 0;
    max-width: 212px;
    margin-right: 12.2%;
    @media (max-width: 1600px) {
      margin-right: 6.2%;
    }
    @media (max-width: $laptop) {
      max-width: unset;
      width: 100%;
      margin-bottom: 32px;
    }

    @media (max-width: $mobile) {
      margin-bottom: 24px;
    }

    img {
      width: 100%;
      height: 100%;
      object-fit: contain;
      object-position: top;
      @media (max-width: $laptop) {
        max-width: 212px;
      }
      @media (max-width: $tablet) {
        max-width: 163px;
      }
    }
  }

  &__subtitle {
    display: inline-block;
    font-size: 20px;
    font-weight: 600;
    line-height: 24px;
    margin-bottom: 16px;
    color: $black;
    @media (max-width: $tablet) {
      margin-bottom: 12px;
    }
  }

  &__feedback {
    display: flex;
    flex-grow: 1;
    justify-content: space-between;
    margin: 0 -10px;
    @media (max-width: $tablet) {
      flex-direction: column;
    }
    @media (max-width: $mobile) {
      margin: 0;
      margin-top: 24px;
    }
  }

  &__col {
    color: #6B7280;
    flex-grow: 1;
    margin: 0 10px;

    @media (max-width: $mobile) {
      margin: 0;
      & + .footer__col {
        margin-top: 24px;
      }
    }


    a {
      transition: opacity .3s;

      &:hover {
        color: $black;
      }
    }
  }

  &__menu {
    font-size: 18px;
    font-weight: 500;
    line-height: 27px;
    @media (max-width: $tablet) {
      font-size: 16px;
      font-weight: 500;
      line-height: 24px;
    }

    li {
      margin-left: -5px;

      & + li {
        margin-top: 2px;
        @media (max-width: $tablet) {
          margin-top: 0;
        }
      }
    }

    a {
      display: inline-block;
      padding: 5px;
      @media (max-width: $tablet) {
      }
    }
  }

  &__socials {
    margin-top: -20px;
    @media (max-width: $laptop) {
      margin-top: 12px;
    }
    @media (max-width: $tablet) {
      margin-top: 24px;
    }


    a {
      display: inline-block;
      @include hover-effect;

      & + a {
        margin-left: 12px;
      }
    }
  }

  &__bottom {
    background: #1EB2E8;
  }

  &__bottom-wrapper {
    color: #fff;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    gap:10px;
    padding: 14px 0;

    @media (max-width: $tablet) {
      font-size: 14px;
      font-weight: 400;
      line-height: 19.1px;
    }


    a {
      @include hover-effect;
    }
  }
}