.search {
  &__title {
    margin-bottom: 32px;
    @media (max-width: $tablet) {
      font-size: 28px;
      line-height: 33.6px;
      margin-bottom: 24px;
    }
    @media (max-width: $mobile) {
      font-size: 24px;
      line-height: 28.8px;
    }
  }

  &__content {
    display: flex;
    flex-wrap: wrap;
    margin: 0 -8px -16px;
    @media (max-width: $mobileMedium) {
      margin: 0 0 -12px;
    }

    .card-item {
      margin: 0 8px 16px;
      width: calc((100% - (16px * 5)) / 5);
      @media (max-width: 1440px) {
        width: calc((100% - (16px * 4)) / 4);
      }
      @media (max-width: $laptop) {
        width: calc((100% - (16px * 3)) / 3);
      }
      @media (max-width: $tablet) {
        width: calc((100% - (16px * 2)) / 2);
      }
      @media (max-width: $mobileMedium) {
        width: 100%;
        margin:0 0 12px;
      }
    }
  }
}