//header start
.header {
  position: relative;
  z-index: 10;
  box-shadow: 0px 4px 24px 0px #ADADAD1A;
  border-radius: 0 0 50px 50px;
  padding-bottom: 20px;
  padding-top: 12px;
  background-color: #fff;
  transition: opacity .3s;


  @media (max-width: $tablet) {
    padding-bottom: 12px;
    border-radius: 0 0 20px 20px;

    &[data-open] {
      border-radius: 0;
    }
  }
  @media (max-width: $mobile) {
    height: 80px;
  }

  &--scrolled-down {
    position: fixed;
    left: 0;
    right: 0;
    opacity: 0;
    z-index: -1;
    visibility: hidden;

    .header__top {
      @media (min-width: 992px) {
        position: absolute;
        z-index: -1;
        visibility: hidden;
      }
    }
  }

  &--scrolled-up {
    position: fixed;
    left: 0;
    right: 0;
    opacity: 1;

    .header__top {
      @media (min-width: 992px) {
        position: absolute;
        z-index: -1;
        visibility: hidden;
      }
    }
  }

  &__top {
    display: flex;
    align-items: center;
  }

  &__town {
    @media (max-width: $mobile) {
      display: none;
    }
  }


  &__logo {
    display: flex;
    flex-shrink: 0;
    width: 102px;
    margin-right: 50px;
    order: -1;
    @media (max-width: $laptop) {
      width: 85px;
      margin-right: 25px;
    }
    @media (max-width: $tablet) {
      width: 80px;
    }

    img {
      max-width: 100%;
      object-fit: contain;
    }
  }

  .delivery-box {
    margin-right: 60px;
    @media (max-width: $laptop) {
      margin-right: 25px;
    }
    @media (max-width: $tablet) {
      margin-left: 24px;
      order: 2;
    }
    @media (max-width: $mobile) {
      justify-content: center;
      width: max-content;
      margin-right: auto;
      margin-bottom: 20px;
      margin-left: 0;
    }
  }

  &__tel {
    margin-left: 23px;
    font-size: 18px;
    font-weight: 500;
    line-height: 21.6px;
    color: $black;
    transition: opacity .3s;
    white-space: nowrap;
    margin-bottom: -2px;
    @media (max-width: $laptop) {
      font-size: 14px;
    }
    @media (max-width: $tablet) {
      display: none;
    }

    &:hover {
      opacity: .7;
    }
  }

  &__burger-menu {
    display: flex;
    margin-left: auto;
    flex-grow: 1;
    font-weight: 500;
    border-radius: 0 0 20px 20px;
    @media (max-width: $laptop) {
      font-size: 16px;
    }
    @media (max-width: $tablet) {
      position: absolute;
      top: 100%;
      left: 0;
      right: 0;
      background: #fff;
      box-shadow: 0px 4px 24px 0px #ADADAD1A;
      display: none;
      flex-direction: column;
      padding: 6px 20px 12px;
      font-size: 18px;
      line-height: 21.6px;
    }
    @media (max-width: $mobile) {
      position: fixed;
      top: 80px;
      bottom: 0;
      overflow-y: auto;
    }


    .header__menu {
      margin-left: auto;
      width: max-content;
      @media (max-width: $tablet) {
        margin-left: -5px;
        margin-bottom: 50px;
      }
    }


    ul {
      display: flex;
      align-items: center;
      @media (max-width: $tablet) {
        flex-direction: column;
        align-items: flex-start;
      }

      li {
        white-space: nowrap;

        & + li {
          margin-left: 14px;
          @media (max-width: $tablet) {
            margin-left: 0;
            margin-top: 10px;
          }
        }
      }

      a {
        display: inline-block;
        padding: 5px;
        transition: opacity .3s;

        &:hover {
          opacity: .7;
        }
      }
    }
  }

  &__bottom {
    display: flex;
    align-items: center;
    margin-top: 12px;
    @media (max-width: $tablet) {
      display: none;
    }
  }

  &__account-btns {
    display: flex;
    align-items: center;
    margin-left: auto;
  }

  &__search {
    flex-grow: 1;
  }

  &__catalog-btn {
    position: absolute;
    z-index: 1;
    top: 50%;
    transform: translateY(-50%);
    left: 3px;
    height: 45px;

    [data-opened='closed'] {
      display: block;
    }

    [data-opened='active'] {
      display: none;
    }

    &[data-active] {
      [data-opened='closed'] {
        display: none;
      }

      [data-opened='active'] {
        display: block;
      }
    }
  }

  &__search {
    position: relative;
    margin-right: 15px;

    input {
      padding-left: 162px;
    }
  }

  &__burger-icon {
    display: none;
    @media (max-width: $tablet) {
      position: relative;
      display: flex;
      flex-shrink: 0;
      justify-content: center;
      align-items: center;
      width: 38px;
      height: 30px;
      padding: 5px;
      cursor: pointer;
      margin-left: 0;
      order: 3;

      &[data-open] {
        span {
          background-color: transparent;

          &::after {
            transform: translateY(7px) rotate(45deg);
          }

          &::before {
            transform: translateY(-9px) rotate(-45deg);
          }
        }
      }


      span {
        display: flex;
        width: 100%;
        height: 3px;
        background: $black;
        border-radius: 14px;
        transition: transform .3s;

        &::after,
        &::before {
          content: '';
          background: $black;
          width: 28px;
          height: 3px;
          border-radius: 14px;
          position: absolute;
          transition: all .3s;

        }

        &::after {
          top: 5px;
        }

        &::before {
          bottom: 6px;
        }
      }
    }
  }

  &__login-btn {
    display: none;
    @media (max-width: $tablet) {
      display: flex;
      align-items: center;
      order: 2;
      margin-left: auto;
      margin-right: 24px;
      font-size: 16px;
      white-space: nowrap;
      font-weight: 500;
      line-height: 21.82px;
      @include hover-effect;

      span {
        display: flex;

        & + span {
          margin-left: 8px;
        }
      }
    }
  }

  &__basket {
    position: absolute;
    top: calc(100% - 5px);
    right: calc(50vw - 900px);
    max-height: calc(100vh - 180px);
    @media (max-width: 1920px) {
      right: calc(50vw - (50% - 60px));
    }
    @media (max-width: $tablet) {
      display: none;
    }

    .header-basket {
      &__list {
        max-height: calc(100vh - 315px);
        overflow-y: auto;
      }
    }
  }
}

.header-basket {
  display: none;
  box-shadow: 0px 4px 24px 0px #ADADAD1A;
  border-radius: 20px;
  width: 380px;
  background: #fff;

  &[data-open] {
    display: block;
    opacity: 1;
    z-index: 1;
    visibility: visible;
  }

  &__list {
    margin-right: -16px;
    padding-right: 16px;
  }

  &__footer {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding-top: 16px;
  }

  &__price {
    font-size: 16px;
    font-weight: 500;
    line-height: 24px;
  }

  &__wrapper {
    padding: 16px;
  }

  &__title-block {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 24px;
  }

  &__title {
    font-size: 20px;
    font-weight: 600;
    line-height: 24px;
  }

  &__empty {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    padding: 60px 0;
  }

  &__icon {
    display: flex;
    margin-bottom: 10px;

    svg,
    img {
      max-width: 100%;
    }
  }

  &__subtitle {
    font-size: 20px;
    font-weight: 600;
    line-height: 24px;
    margin-bottom: 8px;
  }

  &__text {
    font-size: 16px;
    font-weight: 500;
    line-height: 24px;
    color: #6B7280;
  }

  &__clear {
    color: #1EB2E8;
    font-size: 16px;
    font-weight: 500;
    line-height: 24px;
    @include hover-effect;
  }
}

.delivery-box {
  display: flex;
  align-items: center;
  padding: 5px;
  font-size: 14px;
  font-weight: 600;
  line-height: 19.1px;
  color: #9CA5AD;
  border: 1px solid #E9ECF2;
  border-radius: 10px;

  &__choice {
    padding: 9px 12px;

    &--active {
      background: #E8F9FF;
      color: #1EB2E8;
      border-radius: 10px;
      cursor: auto;
    }

    & + .delivery-box__choice {
      margin-left: 12px;
    }
  }

  &--big {
    font-size: 16px;
    line-height: 24.1px;

    .delivery-box__choice {
      flex-grow: 1;
      padding: 11px;

      &--active{
        background-color:#1EB2E8;
        color:#fff;
      }
    }

  }
}

.header-town-btn {
  position: relative;
  display: flex;
  align-items: center;
  color: $blue;
  @media (max-width: $laptop) {
    font-size: 16px;
  }

  &::after {
    content: '';
    position: absolute;
    bottom: -1px;
    right: 0;
    border: 1px dashed #1EB2E8;
    left: 27px;
  }

  &::before {
    content: '';
    display: inline-block;
    width: 25px;
    height: 24px;
    background-image: url("data:image/svg+xml;charset=UTF-8,%3csvg width='25' height='25' viewBox='0 0 25 25' fill='none' xmlns='http://www.w3.org/2000/svg' xmlns:xlink='http://www.w3.org/1999/xlink'%3e%3crect x='0.65625' y='0.707031' width='24' height='24' fill='url(%23pattern0_3376_1673)'/%3e%3cdefs%3e%3cpattern id='pattern0_3376_1673' patternContentUnits='objectBoundingBox' width='1' height='1'%3e%3cuse xlink:href='%23image0_3376_1673' transform='scale(0.00195312)'/%3e%3c/pattern%3e%3cimage id='image0_3376_1673' width='512' height='512' xlink:href='data:image/png;base64,'/%3e%3c/defs%3e%3c/svg%3e ");
  }
}


.header-account-btn {
  display: flex;
  flex-direction: column;
  align-items: center;
  border-right: 1px solid #E9ECF2;
  padding: 0 15px;
  transition: opacity .3s;
  @media (max-width: $laptop) {
    font-size: 14px;
  }

  &:hover {
    opacity: .7;
  }

  &:last-child {
    border-right: none;
  }

  &__icon {
    display: flex;
    margin-bottom: 6px;
  }
}

.header-burger {
  &__tel {
    display: none;
    font-size: 20px;
    font-weight: 500;
    line-height: 24px;
    @include hover-effect;
    @media (max-width: $tablet) {
      display: block;
    }
    @media (max-width: $mobile) {
      font-size: 16px;
      line-height: 24px;
    }
  }

  .language-choice-box {
    margin-bottom: 16px;
  }
}

.language-choice-box {
  display: none;
  @media (max-width: $tablet) {
    display: flex
  }

  &__btn {
    display: flex;
    padding: 5px;
    border-radius: 50%;
    font-size: 14px;
    font-weight: 400;
    line-height: 21px;
    border: 1px solid #9CA5AD;
    color: #9CA5AD;


    &--active {
      border: 1px solid #1EB2E8;
      color: #1EB2E8;
    }

    & + .language-choice-box__btn {
      margin-left: 12px;
    }
  }
}

.header-catalog {
  display: none;
  max-width: 1476px;
  box-shadow: 0px 6px 14px -6px #CBCCCF1F;
  position: absolute;
  top: 100%;
  background: #fff;
  border-radius: 20px;
  opacity: 0;
  visibility: hidden;
  z-index: -1;
  max-height: calc(100vh - 180px);
  overflow-y: auto;
  transition: opacity .3s;

  @media (max-width: 1600px) {
    width: calc(1200px);
    max-width: calc(100% - 120px);
  }

  &[data-open] {
    display: flex;
    opacity: 1;
    z-index: 1;
    visibility: visible;
  }


  &__categories {
    width: 218px;
    flex-shrink: 0;
    padding: 24px 12px;
    border-right: 1px solid #F5F7F9
  }

  &__category + .header-catalog__category {
    margin-top: 12px;
  }

  &__category {
    padding: 9px 12px;
    display: flex;
    align-items: center;
    font-size: 14px;
    font-weight: 500;
    line-height: 21px;
    cursor: pointer;

    &--active {
      position: relative;
      background: #F9F9F9;
      padding-right: 30px;
      border-radius: 15px;

      &::after {
        content: '';
        position: absolute;
        background-image: url("data:image/svg+xml;charset=UTF-8,%3csvg width='7' height='10' viewBox='0 0 7 10' fill='none' xmlns='http://www.w3.org/2000/svg'%3e%3cpath d='M1.57422 8.70703L5.57422 4.70703L1.57422 0.707031' stroke='%23283238' stroke-width='1.2' stroke-linecap='round' stroke-linejoin='round'/%3e%3c/svg%3e ");
        width: 7px;
        height: 10px;
        right: 20px;
        top: 50%;
        transform: translateY(-50%);
      }
    }

    span {
      display: flex;

      & + span {
        margin-left: 12px;
      }
    }
  }

  &__subcategories {
    position: relative;
    padding: 24px;
    width: 786px;
    max-height: 440px;
  }

  &__sub {
    position: absolute;
    display: none;
    top: 0;
    opacity: 0;
    z-index: -1;
    visibility: hidden;

    &--active {
      display: block;
      position: relative;
      opacity: 1;
      z-index: 1;
      visibility: visible;
      transition: opacity .3s;
    }
  }

  &__sub-title {
    font-size: 20px;
    font-weight: 600;
    line-height: 24px;
    margin-bottom: 24px;
  }

  &__sub-items {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    margin-bottom: -14px;
    @media (max-width: 1600px) {
      flex-direction: column;
      font-size: 16px;
      line-height: 20px;
    }

    a {
      width: calc(50% - 20px);
      margin-bottom: 14px;
      @include hover-effect;
      @media (max-width: 1600px) {
        width: 100%;
      }
    }
  }

  &__banner {
    padding: 24px 20px;
    border-left: 1px solid #F5F7F9;
    flex-shrink: 0;
    margin-left: auto;
    max-width: 400px;

  }
}

.stock-banner {
  position: relative;
  border-radius: 15px;
  overflow: hidden;
  @media (max-width: $laptop) {
    height: 40vw;
  }
  @media (max-width: $tablet) {
    height: 54vw;
  }
  @media (max-width: 640px) {
    height: 95vw;
  }

  &.t-black {
    color: $black;

    .stock-banner__info-wrapper {
      color: $black;
    }
  }

  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }

  &__info-wrapper {
    position: absolute;
    top: 35px;
    width: 100%;
    color: #fff;
  }

  &__text {
    font-size: 18px;
    font-weight: 400;
    line-height: 27px;
    text-align: center;

    span {
      font-size: 20px;
      font-weight: 600;
      line-height: 30px;
      text-align: center;
      padding: 6px 12px;
      color: #FCC900;
      background: $black;
      border-radius: 5px;
    }
  }

  &__subtitle {
    font-size: 14px;
    font-weight: 500;
    line-height: 14px;
    letter-spacing: 0.03em;
    text-align: center;
    margin-bottom: 15px;
  }

  &__title {
    font-size: 36px;
    font-weight: 600;
    line-height: 43.2px;
    text-align: center;
    margin-bottom: 13px;
    @media (max-width: $tablet) {
      font-size: 28px;
      font-weight: 700;
      line-height: 33.6px;
    }
    @media (max-width: $mobile) {
      font-size: 20px;
      font-weight: 600;
      line-height: 24px;
      margin-bottom: 8px;
    }
  }
}

.timer {
  display: flex;
  justify-content: center;

  &__item {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    min-width: 56px;

    &::after {
      content: ':';
      position: absolute;
      top: 50%;
      font-size: 24px;
      font-weight: 400;
      line-height: 36px;
      right: -11px;
      transform: translateY(-73%);
    }

    &:last-child {
      &::after {
        content: none;
      }
    }

    & + .timer__item {
      margin-left: 22px;
    }

    span {
      &:nth-child(1) {
        font-size: 24px;
        font-weight: 400;
        line-height: 36px;
        text-align: center;
        @media (max-width: $tablet) {

        }
      }

      &:nth-child(2) {
        font-size: 12px;
        font-weight: 400;
        line-height: 12px;
        letter-spacing: 0.03em;
      }
    }
  }
}

.header-search-box {
  position: absolute;
  top: 100%;
  left: 140px;
  background: #fff;
  width: calc(100% - 140px);
  box-shadow: 0px 1px 6px 0px #18274B0D;
  border-radius: 15px;
  padding: 16px;
  opacity: 0;
  z-index: -1;
  visibility: hidden;
  transition: opacity .3s;

  &[data-search-open] {
    opacity: 1;
    z-index: 1;
    visibility: visible;
  }

  &__all {
    display: inline-block;
    font-size: 16px;
    font-weight: 500;
    line-height: 24px;
    margin-top: 16px;
    color: #1EB2E8;
    @include hover-effect;
  }

  .search-line {
    display: flex;

    & + .search-line {
      margin-top: 12px;
    }

    &__ava {
      width: 70px;
      height: 54px;
      margin-right: 12px;

      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
    }

    &__info {
      flex-grow: 1;
      display: flex;
      flex-direction: column;
      justify-content: center;
    }

    &__name {
      font-size: 16px;
      font-weight: 500;
      line-height: 20px;
    }

    &__category {
      display: inline-block;
      font-size: 14px;
      font-weight: 400;
      line-height: 21px;
      @include hover-effect;
    }

    &__price {
      width: 10%;
      display: flex;
      justify-content: flex-end;
      align-items: center;
      margin-left: auto;
    }
  }
}

.basket-item {
  position: relative;
  padding: 12px 0;
  padding-right: 34px;
  border-bottom: 1px solid #F1F3F9;

  &__wrapper {
  }

  &__top-info {
    display: flex;
    margin-bottom: 15px;
  }

  &__title {
  }

  &__img {
    flex-shrink: 0;
    width: 45px;
    height: 45px;
    object-fit: contain;
    margin-right: 8px;

    img {
      width: 100%;
      height: auto;
    }
  }

  &__logic {
    display: flex;
    align-items: center;
    padding-left: 53px;
  }

  &__amount {
    margin-right: 8px;
  }

  &__title {
    font-size: 14px;
    font-weight: 500;
    line-height: 21px;
  }

  &__price {
    font-size: 14px;
    font-weight: 500;
    line-height: 21px;
    margin-right: 8px;
  }

  &__quantity {
    font-size: 12px;
    line-height: 18px;
    color: #9CA5AD;
  }

  &__delete {
    position: absolute;
    cursor: pointer;
    z-index: 1;
    width: 30px;
    height: 30px;
    display: flex;
    justify-content: center;
    align-items: center;
    top: 0;
    right: 0;

    &::before {
      content: '';
      display: inline-block;
      width: 14px;
      height: 16px;
      background-position: center;
      background-repeat: no-repeat;
      background-image: url("data:image/svg+xml;charset=UTF-8,%3csvg width='14' height='16' viewBox='0 0 14 16' fill='none' xmlns='http://www.w3.org/2000/svg'%3e%3cpath d='M1 3.9987H2.33333M2.33333 3.9987H13M2.33333 3.9987V13.332C2.33333 13.6857 2.47381 14.0248 2.72386 14.2748C2.97391 14.5249 3.31304 14.6654 3.66667 14.6654H10.3333C10.687 14.6654 11.0261 14.5249 11.2761 14.2748C11.5262 14.0248 11.6667 13.6857 11.6667 13.332V3.9987M4.33333 3.9987V2.66536C4.33333 2.31174 4.47381 1.9726 4.72386 1.72256C4.97391 1.47251 5.31304 1.33203 5.66667 1.33203H8.33333C8.68696 1.33203 9.02609 1.47251 9.27614 1.72256C9.52619 1.9726 9.66667 2.31174 9.66667 2.66536V3.9987M5.66667 7.33203V11.332M8.33333 7.33203V11.332' stroke='%239CA5AD' stroke-width='1.3' stroke-linecap='round' stroke-linejoin='round'/%3e%3c/svg%3e");
    }
  }
}

//header end