.error {
  margin: 64px 0 100px;
  @media (max-width: $tablet) {
    margin: 64px 0;
  }
  @media (max-width: $mobile) {
    margin: 50px 0;
  }

  &__wrapper {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    width: 100%;
  }

  &__icon {
    display: flex;
    margin-bottom: 50px;
    @media (max-width: $mobile) {
      margin-bottom: 32px;
    }

    svg {
      max-width: 100%;
    }
  }

  &__subtitle {
    font-size: 32px;
    font-weight: 700;
    line-height: 38.4px;
    text-align: center;
    margin-bottom: 16px;
    @media (max-width: $tablet) {
      font-size: 28px;
      line-height: 33.6px;
    }
    @media (max-width: $mobile) {
      font-size: 20px;
      font-weight: 600;
      line-height: 24px;
      margin-bottom: 8px;
    }
  }

  &__text {
    color: #6B7280;
    margin-bottom: 24px;
    @media (max-width: $tablet) {
      font-size: 16px;
      font-weight: 500;
      line-height: 24px;
    }
  }
}