.auth-page {
  header {
    display: none;
  }

  footer {
    display: none;
  }

  main {
    display: flex;
    flex-direction: column;
  }
}

.auth {
  position: relative;
  flex-grow: 1;
  margin: 0;

  &__back {
    position: fixed;
    height: 100vh;
    left: 0;
    top: 0;
    bottom: 0;
    width: 50%;
    object-position: center;
    object-fit: cover;
    background-size: cover;
    @media (max-width: $tablet) {
      width: 100%;
    }
  }

  &__wrapper {
    display: flex;
    height: 100%;
  }

  &__left {
    position: fixed;
    height: 100vh;
    left: 0;
    top: 0;
    bottom: 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 50%;
    flex-shrink: 0;
    background-size: cover;
    padding: 40px 0;
    @media (max-width: $tablet) {
      display: none;
    }
  }

  &__right {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 50%;
    flex-shrink: 0;
    margin-left: auto;
    @media (max-width: $tablet) {
      width: 100%;
      margin: 60px 0;
    }
  }

  &__body {
    position: relative;
    z-index: 1;
    max-width: 550px;
    padding: 40px 0;
    @media (max-width: $tablet) {
      background-color: #fff;
      border-radius: 15px;
      padding: 32px;
    }
    @media (max-width: $mobileMedium) {
      max-width: calc(100% - 20px);
      padding: 20px 12px;
    }
  }

  &--registration {
    .auth-body__subtitle {
      margin-bottom: 12px;
    }
  }
}

.auth-banner {
  &__slider {
    width: 60%;
  }

  &__pagination {
    display: flex;
    justify-content: center;
    margin-top: 40px;

    .swiper-pagination-bullet {
      background: #96BFFF;
      width: 8px;
      height: 8px;
      display: inline-block;
      position: relative;
      opacity: 1;
    }

    .swiper-pagination-bullet.swiper-pagination-bullet-active {
      background: #FFFFFF;
      width: 24px;
      border-radius: 10px;
    }
  }
}

.auth-slide {
  text-align: center;
  color: #fff;

  &__picture {
    display: flex;
    margin-bottom: 20px;
    max-height: 48vh;

    img {
      width: 100%;
      object-fit: contain;
      object-position: center;
    }
  }

  &__title {
    font-size: 40px;
    font-weight: 600;
    line-height: 54.56px;
    margin-bottom: 12px;
  }

  &__subtitle {
    font-size: 24px;
    font-weight: 400;
    line-height: 32.74px;
    @media (max-width: $tablet) {
      font-size: 18px;
      line-height: 24.74px;
    }
    @media (max-width: $mobile) {
      font-size: 16px;
      line-height: 24px;
    }
  }
}

.auth-body {
  &__title {
    text-align: center;
    margin-bottom: 36px;
    @media (max-width: $tablet) {
      margin-bottom: 20px;
    }
    @media (max-width: $mobile) {
      margin-bottom: 12px;
    }
  }

  &__choice {
    display: none;

    &[data-choice-active] {
      display: block;
    }
  }

  .auth-body__choose {
    margin-bottom: 36px;
    @media (max-width: $tablet) {
      margin-bottom: 24px;
    }
    @media (max-width: $mobileMedium) {
      margin-bottom: 12px;
    }

    .form-input {
      width: auto;

      & + .form-input {
        margin: 0;
        margin-left: 38px;
        @media (max-width: $mobileMedium) {
          margin-left: 0;
          margin-top: 6px;
        }
      }
    }
  }

  &__choose {
    display: flex;
    align-items: center;
    @media (max-width: $mobileMedium) {
      flex-direction: column;
    }
  }

  &__subtitle {
    color: #8F8F8F;
    font-size: 20px;
    font-weight: 400;
    line-height: 30px;
    text-align: center;
    margin-bottom: 36px;
    @media (max-width: $tablet) {
      font-size: 18px;
      line-height: 24px;
      margin-bottom: 24px;
    }
    @media (max-width: $mobile) {
      font-size: 16px;
      line-height: 20px;
      margin-bottom: 20px;
    }
  }

  .form-input {
    & + .form-input {
      margin-top: 16px;
    }
  }
}

.auth-form {
  width: 470px;
  margin:0 auto;
  @media (max-width: $mobileMedium) {
    width: 100%;
  }

  &__disclaimer {
    font-size: 14px;
    font-weight: 400;
    line-height: 19.1px;
    letter-spacing: 0.8999999761581421px;
    color: #88929B;
    margin-top: 12px;
  }

  &__sublink {
    text-align: right;
    margin-top: 12px;
    color: #1EB2E8;
    @media (max-width: $mobile) {
      font-size: 14px;
      line-height: 21px;
    }

    a {
      @include hover-effect;
    }
  }

  &__buttons {
    margin-top: 40px;
    margin-bottom: 17px;
    @media (max-width: $tablet) {
      margin-top: 32px;
    }

    &.auth-form__buttons--row {
      display: flex;
      align-items: center;
      @media (max-width: $mobileMedium) {
        flex-direction: column;
        align-items: flex-start;
      }

      .btn {
        margin-right: 16px;
        width: auto;
        @media (max-width: $mobileMedium) {
          margin-right: 0;
          width: 100%;
          margin-bottom: 16px;
        }
      }
    }
  }

  .btn {
    width: 100%;
  }

  &__add-info {
    font-size: 16px;
    line-height: 21.82px;
    letter-spacing: 0.8999999761581421px;
    @media (max-width: $mobile) {
      font-size: 14px;
      line-height: 19.11px;
      letter-spacing: 0.8999999761581421px;
    }

    a {
      color: #1EB2E8;
      @include hover-effect;
    }
  }

  .form-input__error-text {
    position: relative;
  }
}

.auth-success {
  @media (max-width: $mobile) {
    padding: 12px;
  }

  &__icon {
    display: flex;
    justify-content: center;
    margin-bottom: 32px;
    @media (max-width: $tablet) {
      margin-bottom: 24px;
    }
    @media (max-width: $mobile) {
      margin-bottom: 16px;
    }
  }

  &__title {
    font-size: 40px;
    font-weight: 500;
    line-height: 48px;
    text-align: center;
    margin-bottom: 12px;
    @media (max-width: $tablet) {
      font-size: 32px;
      font-weight: 700;
      line-height: 38.4px;
      margin-bottom: 20px;
    }
    @media (max-width: $mobile) {
      font-size: 28px;
      line-height: 33.6px;
    }
    @media (max-width: $mobileMedium) {
      font-size: 24px;
      line-height: 30px;
    }
  }

  &__subtitle {
    font-size: 20px;
    font-weight: 400;
    line-height: 30px;
    text-align: center;
    margin-bottom: 32px;
    @media (max-width: $mobileMedium) {
      font-size: 18px;
      line-height: 24px;
    }

  }

  &__btn {
    width: 100%;
  }
}