.btn {
  position: relative;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  font-size: 16px;
  font-weight: 500;
  line-height: 24px;
  background-color: $blue;
  color: #fff;
  border-radius: 15px;
  padding: 4px 12px;
  cursor: pointer;
  transition: background-color .3s;

  &__icon {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 24px;
    height: 24px;
    margin-right: 8px;

    svg {
      max-width: 24px;
      max-height: 24px;
    }
  }


  &--l {
    font-size: 18px;
    line-height: 28px;
    padding: 11px 16px;
    @media (max-width: $mobile) {
      font-size: 16px;
      line-height: 24px;
      padding: 8px 14px;
    }
  }

  &--m {
    padding: 8px 14px;

    .btn__icon {
      width: 20px;
      height: 20px;

      svg {
        max-width: 20px;
        max-height: 20px;
      }
    }
  }

  &--red {
    background: #D84460;
    @include hover-effect;
  }

  &--grey {
    background-color: #F6F7F8;
    color: $black;
    @include hover-effect;
  }

  &--grey-line {
    background-color: transparent;
    border: 1px solid #E4E7EC;
    color: #6B7280;
    transition: all .3s;

    &:hover {
      box-shadow: 0px 1px 2px 0px #1B242C1F;
      background: #898D90;
      color: #fff;
    }

    &:focus {
      &::after {
        content: '';
        position: absolute;
        display: block;
        right: -2px;
        top: -2px;
        bottom: -2px;
        left: -2px;
        border: 1px solid #DADDE1;
        border-radius: 17px;
      }
    }

    &:active {
      background: #283238;
      color: #fff;
    }
  }

  &--blue-bb {
    color: #1EB2E8;
    background: #fff;
    border: 1px solid #1EB2E8;

    &:hover {
      background: #F6F7F8;
    }

    &:focus {
      &::after {
        content: '';
        position: absolute;
        display: block;
        right: -3px;
        top: -3px;
        bottom: -3px;
        left: -3px;
        border: 1px solid #42C3F3;
        border-radius: 17px;
      }
    }

    &:active,
    &--active,
    &[data-active] {
      background: #1E90B9;
      color: #fff;
    }

  }

  &--blue-tb {
    background-color: transparent;
    border: 1px solid #F6F7F8;

    &:hover {
      background: #1AA0D2;
    }

    &:focus {
      &::after {
        content: '';
        position: absolute;
        display: block;
        right: -2px;
        top: -2px;
        bottom: -2px;
        left: -2px;
        border: 1px solid #42C3F3;
        border-radius: 17px;
      }
    }

    &:disabled {
      background: #DCDCDC;
      cursor: auto;
      pointer-events: none;
    }

    &:active,
    &--active,
    &[data-active] {
      background: #1E90B9;
    }

    &:disabled,
    &--disabled {
      background: #5CC0E4;
      cursor: auto;
      pointer-events: none;
    }
  }

  &--blue {
    background-color: $blue;

    &:hover {
      background: #1AA0D2;
    }

    &:focus {
      &::after {
        content: '';
        position: absolute;
        display: block;
        right: -2px;
        top: -2px;
        bottom: -2px;
        left: -2px;
        border: 1px solid #42C3F3;
        border-radius: 17px;
      }
    }

    &:active,
    &--active,
    &[data-active] {
      background: #1E90B9;
    }

    &:disabled,
    &--disabled {
      background: #5CC0E4;
      cursor: auto;
      pointer-events: none;
    }
  }
}

.btn-underline {
  position: relative;
  display: inline-flex;
  align-items: center;
  font-size: 20px;
  font-weight: 500;
  align-self: baseline;
  padding-bottom: 5px;
  margin-right: 3px;
  @include hover-effect;

  &::before {
    content: '';
    position: absolute;
    width: calc(100% - 28px);
    height: 1px;
    background-color: $black;
    bottom: 0;
    right: 28px;
    transition: width .3s;
    pointer-events: none;
  }

  &:hover {
    &::before {
      width: 0;
      left: auto;
    }
  }


  &::after {
    content: '';
    display: inline-block;
    width: 25px;
    height: 25px;
    transform: translateX(3px);
    background-image: url("data:image/svg+xml;charset=UTF-8,%3csvg width='25' height='25' viewBox='0 0 25 25' fill='none' xmlns='http://www.w3.org/2000/svg'%3e%3cpath fill-rule='evenodd' clip-rule='evenodd' d='M10.4163 8.19385C10.7545 7.82961 11.324 7.80852 11.6882 8.14674L15.8882 12.0467C16.0716 12.217 16.1758 12.456 16.1758 12.7063C16.1758 12.9565 16.0716 13.1955 15.8882 13.3658L11.6882 17.2658C11.324 17.604 10.7545 17.5829 10.4163 17.2187C10.078 16.8544 10.0991 16.285 10.4634 15.9467L13.9531 12.7063L10.4634 9.46577C10.0991 9.12755 10.078 8.55809 10.4163 8.19385Z' fill='%23283238'/%3e%3c/svg%3e ");
    transition: transform .2s;
  }

  &:hover {
    &::after {
      transform: translateX(-1px);
    }
  }
}

.btn-underline-pic {
  display: flex;
  align-items: center;
  color: #1EB2E8;
  text-decoration: underline;
  @include hover-effect;

  &__icon {
    display: flex;
    margin-right: 4px;
  }

  &__text {
    font-size: 16px;
    font-weight: 400;
    line-height: 24.37px;
  }

  &--grey {
    color: #6B7280;
  }
}

.file-link {
  display: flex;

  &__icon {
    display: flex;
    width: 24px;
    height: 24px;
    margin-right: 8px;
  }

  &__info {
    display: flex;
    flex-direction: column;
  }

  &__title {
    font-size: 16px;
    font-weight: 500;
    line-height: 24px;
    margin-bottom: 4px;
  }

  &__text {
    font-size: 14px;
    font-weight: 500;
    line-height: 21px;
    color: #9CA5AD;
  }
}