.basket {
  &__wrapper {
    display: flex;
    justify-content: space-between;
    @media (max-width: $laptop) {
      flex-wrap: wrap;
    }
  }

  &__title-block {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 16px;
    @media (max-width: $mobile) {
      flex-wrap: wrap;
    }
  }

  &__title {
    @media (max-width: $mobile) {
      width: 100%;
    }
  }

  &__clear {
    @media (max-width: $mobile) {
      margin-top: 10px;
    }
  }

  &__order-info {
    width: 500px;
    flex-shrink: 0;
    margin-left: 5%;
    @media (max-width: 1440px) {
      width: 360px;
    }
    @media (max-width: $laptop) {
      width: 100%;
      margin-left: 0;
      margin-top: 50px;
    }
    @media (max-width: $mobile) {
      margin-top: 40px;
    }
  }

  &__clear {
    font-size: 16px;
    font-weight: 500;
    line-height: 24px;
    color: #1EB2E8;
    @include hover-effect;
  }

  .basket-advertisement {
    margin-bottom: 16px;
  }

  &__clear-unavail {
    color: $black;
    text-decoration: underline;
    @include hover-effect;
  }

  &__body {
    flex-grow: 1;
  }

}

.basket-advertisement {
  display: inline-block;

  img {
    width: 100%;
    height: auto;
  }
}

.basket-card {
  padding: 26px 0;

  @media (max-width: $tablet) {
    padding: 20px 0;
  }
  @media (max-width: $mobile) {
    padding: 13px 0;
  }

  .btn {
    white-space: nowrap;
    @media (max-width: $tablet) {
      padding: 5px 16px;
      font-size: 16px;
      font-weight: 500;
      line-height: 24px;
    }
    @media (max-width: $mobile) {
      padding: 3px 12px;
    }
  }

  &--unavailable {
    .basket-card {
      &__info {
        opacity: .3;
        pointer-events: none;
        cursor: default;
      }
    }
  }

  &--deleted {
    .basket-card__title {
      text-decoration: line-through;
    }

    .basket-card__picture {
      display: none;
    }
  }

  & + .basket-card {
    border-top: 1px solid #F1F3F9;
  }

  &__wrapper {
    display: flex;
    align-items: center;
    @media (max-width: $mobile) {
      position: relative;
      flex-wrap: wrap;
    }
  }

  &__info {
    display: flex;
    align-items: center;
    margin-right: auto;
    @media (max-width: $mobile) {
      width: 100%;
      margin-right: 40px;
      margin-bottom: 15px;
    }
    @media (max-width: $mobile) {
      margin-bottom: 12px;
    }
  }

  &__logic {
    display: flex;
    align-items: center;
    @media (max-width: $mobile) {
      margin-left: 53px;
    }
    @media (max-width: 360px) {
      flex-wrap: wrap;
      margin-left: 0;
    }

    .basket-amount {
      margin-right: 32px;
      @media (max-width: $tablet) {
        padding: 0 6px;
      }
      @media (max-width: $mobile) {
        padding: 6px;
      }

      &__less,
      &__more {
        width: 40px;
        height: 40px;
        @media (max-width: $mobile) {
          width: 20px;
          height: 20px;
        }
      }

      &__count {
        font-size: 18px;
        font-weight: 500;
        line-height: 27px;
        text-align: center;
        width: 62px;
        @media (max-width: $mobile) {
          font-size: 14px;
          line-height: 20px;
          width: 50px;
        }
      }
    }
  }

  &__value {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    @media (max-width: $mobile) {
      flex-direction: row;
    }
  }

  &__price {
    font-size: 24px;
    font-weight: 700;
    line-height: 28.8px;
    text-align: center;
    white-space: nowrap;
    @media (max-width: $tablet) {
      font-size: 20px;
      line-height: 24px;
    }
    @media (max-width: $mobile) {
      font-size: 14px;
      font-weight: 500;
      line-height: 21px;
      margin-right: 8px;
    }
  }

  &__quantity {
    font-size: 14px;
    font-weight: 400;
    line-height: 21px;
    color: #9CA5AD;
    white-space: nowrap;
  }

  &__picture {
    display: inline-flex;
    width: 90px;
    height: 90px;
    margin-right: 20px;
    @include hover-effect;
    @media (max-width: $tablet) {
      width: 70px;
      height: 70px;
    }
    @media (max-width: $mobile) {
      width: 45px;
      height: 45px;
      margin-right: 8px;
    }

    img {
      max-width: 100%;
      height: auto;
      object-fit: contain;
    }
  }

  &__title {
    display: inline-block;
    font-size: 18px;
    font-weight: 500;
    line-height: 21.6px;
    @include hover-effect;
    @media (max-width: $tablet) {
      font-size: 16px;
      margin-right: 20px;
    }
    @media (max-width: $mobile) {
      font-size: 14px;
    }
  }

  &__delete {
    display: inline-flex;
    justify-content: center;
    align-items: center;
    width: 40px;
    height: 40px;
    cursor: pointer;
    margin-left: 20px;
    @include hover-effect;
    @media (max-width: $mobile) {
      position: absolute;
      top: 0;
      right: 0;
    }

    &::before {
      content: '';
      background-image: url("data:image/svg+xml;charset=UTF-8,%3csvg width='24' height='24' viewBox='0 0 24 24' fill='none' xmlns='http://www.w3.org/2000/svg'%3e%3cpath d='M3 6H5H21' stroke='%239CA5AD' stroke-width='1.5' stroke-linecap='round' stroke-linejoin='round'/%3e%3cpath d='M19 6V20C19 20.5304 18.7893 21.0391 18.4142 21.4142C18.0391 21.7893 17.5304 22 17 22H7C6.46957 22 5.96086 21.7893 5.58579 21.4142C5.21071 21.0391 5 20.5304 5 20V6M8 6V4C8 3.46957 8.21071 2.96086 8.58579 2.58579C8.96086 2.21071 9.46957 2 10 2H14C14.5304 2 15.0391 2.21071 15.4142 2.58579C15.7893 2.96086 16 3.46957 16 4V6' stroke='%239CA5AD' stroke-width='1.5' stroke-linecap='round' stroke-linejoin='round'/%3e%3cpath d='M10 11V17' stroke='%239CA5AD' stroke-width='1.5' stroke-linecap='round' stroke-linejoin='round'/%3e%3cpath d='M14 11V17' stroke='%239CA5AD' stroke-width='1.5' stroke-linecap='round' stroke-linejoin='round'/%3e%3c/svg%3e ");
      width: 24px;
      height: 24px;
      display: inline-block;
    }
  }

  &__status {
    color: #6B7280;
    margin-right: 40px;
    white-space: nowrap;
    @media (max-width: $tablet) {
      font-size: 16px;
      font-weight: 500;
      line-height: 24px;
      margin-right: 32px;
    }
    @media (max-width: $mobile) {
      font-size: 14px;
      font-weight: 400;
      line-height: 21px;
      margin-right: 12px;
    }
  }
}

.basket-info {
  &__title-block {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 32px;
    @media (max-width: $tablet) {
      margin-bottom: 26px;
    }
    @media (max-width: $mobile) {
      margin-bottom: 16px;
    }
  }

  &__title {
    margin-bottom: 0;
    @media (max-width: $tablet) {
      font-size: 24px;
      font-weight: 700;
      line-height: 28.8px;
    }
    @media (max-width: $mobile) {
      font-size: 20px;
      font-weight: 600;
      line-height: 24px;
    }
  }

  &__amount {
    font-size: 20px;
    font-weight: 600;
    line-height: 24px;
    color: #6B7280;
    @media (max-width: $mobile) {
      font-size: 18px;
      font-weight: 500;
      line-height: 27px;
    }
  }

  &__list {
    margin-bottom: 32px;
    @media (max-width: $mobile) {
      margin-bottom: 20px;
    }
  }

  &__row {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 20px;
    @media (max-width: $tablet) {
      margin-bottom: 12px;
    }

    span {
      &:nth-child(1) {
        font-size: 20px;
        font-weight: 500;
        line-height: 24px;
        color: #6B7280;
        @media (max-width: $tablet) {
          font-size: 18px;
          font-weight: 500;
          line-height: 27px;
        }
        @media (max-width: $mobile) {
          font-size: 16px;
          font-weight: 500;
          line-height: 24px;
        }
      }

      &:nth-child(2) {
        font-size: 20px;
        font-weight: 600;
        line-height: 24px;
        @media (max-width: $tablet) {
          font-size: 18px;
          font-weight: 500;
          line-height: 21.6px;
        }
        @media (max-width: $mobile) {
          font-size: 16px;
          font-weight: 500;
          line-height: 24px;
        }
      }
    }
  }

  .disclaimer {
    margin-bottom: 14px;
  }

  &__sales-progress {
    margin-bottom: 20px;
    @media (max-width: $tablet) {
      margin-bottom: 12px;
    }
  }

  &__promocode {
    margin-top: 32px;
    margin-bottom: 24px;
    @media (max-width: $tablet) {
      margin: 24px 0;
    }
    @media (max-width: $mobile) {
      margin: 20px 0;
    }
  }

  &__submit {
    width: 100%;
  }

  &__sum {
    display: flex;
    justify-content: space-between;
    font-size: 24px;
    font-weight: 700;
    line-height: 28.8px;
    margin-bottom: 30px;
    @media (max-width: $mobile) {
      font-size: 20px;
      font-weight: 600;
      line-height: 24px;
      margin-bottom: 20px;
    }
  }
}

.sales-progress {
  display: flex;

  &__title {
    white-space: nowrap;
    font-size: 14px;
    font-weight: 500;
    line-height: 21px;
    margin-bottom: 12px;
  }

  &__sum {
    white-space: nowrap;
    font-size: 12px;
    font-weight: 400;
    line-height: 18px;
  }

  &__part {
    position: relative;
    flex-grow: 1;
    text-align: center;
    color: #9CA5AD;
    width: 30%;

    &::before {
      content: '';
      position: absolute;
      width: 100%;
      height: 4px;
      display: inline-block;
      left: 0;
      top: 50%;
      transform: translateY(-50%);
      background-color: #EFF0F3;
    }


    &:last-child {
      text-align: right;
      width: 20%;

      &::before {
        border-bottom-right-radius: 18px;
        border-top-right-radius: 18px;
      }
    }

    &:first-child {
      text-align: left;
      width: 20%;


      &::before {
        border-bottom-left-radius: 18px;
        border-top-left-radius: 18px;
      }
    }

    &--done {
      color: #3A3A3A;

      &::before {
        background-color: #19CF62;
      }
    }
  }
}

