    width: 18px;
    height: 1em;
    background-size: contain;
    background-repeat: no-repeat;
  }
}

.checkout-info {
  &__block {
    background: #F8F9F9;
    padding: 30px;
    border-radius: 30px;
    @media (max-width: $tablet) {
      padding: 16px;
    }

    & + .checkout-info__block {
      margin-top: 30px;
      @media (max-width: $tablet) {
        margin-top: 20px;
      }
    }
  }

  &__title {
    font-size: 24px;
    font-weight: 700;
    line-height: 28.8px;
    margin-bottom: 24px;
    @media (max-width: $tablet) {
      margin-bottom: 20px;
    }
    @media (max-width: $mobile) {
      font-size: 20px;
      font-weight: 600;
      line-height: 24px;
    }
  }

  .checkout-info__label {
    margin-bottom: 6px;
    font-size: 16px;
    font-weight: 500;
    line-height: 24px;

  }

  &__current-manager,
  &__current-delivery {
    margin-top: 12px;
  }

  &__del-types {
    display: flex;
    justify-content: space-between;
    @media (max-width: $mobile) {
      flex-wrap: wrap;
      margin-bottom: -8px;
    }

    .delivery-radio {
      width: calc(50% - 6px);
      @media (max-width: $mobile) {
        width: 100%;
        margin: 0 0 8px;
      }
    }
  }

  &__payments {
    display: flex;
    flex-wrap: wrap;
    margin: 0 -6px -16px;
    @media (max-width: $tablet) {
      margin: 0 0 -12px;
    }

    .payment-item {
      height: auto;
      width: calc(50% - 12px);
      margin: 0 6px 16px;
      @media (max-width: $tablet) {
        width: 100%;
        margin: 0 0 12px;
      }
    }
  }
}

.current-data {
  position: relative;
  background: #fff;
  padding: 24px;
  border: 1px solid #E9ECF2;
  border-radius: 15px;
  @media (max-width: $tablet) {
    padding: 12px;
  }
  @media (max-width: $mobile) {
    padding: 12px 36px 12px 12px;
  }

  &__dates {
    margin-top: 24px;
  }

  &__date-boxes {
    display: flex;
    flex-wrap: wrap;
    margin: -4px;
    @media (max-width: 360px) {
      margin: -2px;
    }

    .form-radio-box {
      margin: 4px;
      @media (max-width: 360px) {
        margin: 2px;
      }
    }
  }

  &__name {
    display: flex;
    align-items: center;
    font-size: 18px;
    font-weight: 500;
    line-height: 27px;
    margin-bottom: 12px;
    @media (max-width: $mobile) {
      font-size: 16px;
      line-height: 24px;
      flex-direction: column;
      align-items: flex-start;
    }
  }

  &__icon {
    display: flex;
    margin-right: 12px;
    @media (max-width: $mobile) {
      margin-bottom: 4px;
    }
  }

  &__props {
    display: flex;
    flex-wrap: wrap;
    gap: 32px;
    @media (max-width: $tablet) {
      gap: 20px;
    }
    @media (max-width: $mobile) {
      gap: 16px;
    }
    @media (max-width: $mobileMedium) {
      gap: 8px;
    }
  }

  &__text {
    font-size: 16px;
    font-weight: 500;
    line-height: 24px;
    color: #6B7280;
    @media (max-width: $mobile) {
      font-size: 14px;
      font-weight: 400;
      line-height: 21px;
    }
  }

  &__prop {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;

    span {
      &:nth-child(1) {
        font-size: 12px;
        font-weight: 400;
        line-height: 16.8px;
        color: #9CA5AD;
        margin-bottom: 2px;
      }

      &:nth-child(2) {
        font-size: 14px;
        font-weight: 400;
        line-height: 21px;
      }
    }
  }

  &__delete {
    position: absolute;
    top: 24px;
    right: 24px;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    border-radius: 8px;
    background: #F6F7F8;
    width: 34px;
    height: 34px;
    @media (max-width: $tablet) {
      top: 12px;
      right: 12px;
    }
    @media (max-width: $mobile) {
      width: 24px;
      height: 24px;
    }

    &::before {
      content: '';
      background-image: url("data:image/svg+xml;charset=UTF-8,%3csvg width='18' height='18' viewBox='0 0 18 18' fill='none' xmlns='http://www.w3.org/2000/svg'%3e%3cg clip-path='url(%23clip0_2904_21775)'%3e%3cpath fill-rule='evenodd' clip-rule='evenodd' d='M11.6006 1.84172C12.2045 1.23775 13.0237 0.898438 13.8779 0.898438C15.6565 0.898438 17.0984 2.34034 17.0984 4.11902C17.0984 4.97317 16.7591 5.79234 16.1552 6.39632L5.65073 16.9007C5.52415 17.0273 5.35246 17.0984 5.17344 17.0984H1.57344C1.20065 17.0984 0.898438 16.7962 0.898438 16.4234V12.8234C0.898438 12.6444 0.969553 12.4727 1.09614 12.3461L11.6006 1.84172ZM13.8779 2.24844C13.3817 2.24844 12.906 2.44552 12.5552 2.79632L12.203 3.14844L14.8484 5.79384L15.2006 5.44173C15.5514 5.09092 15.7484 4.61513 15.7484 4.11902C15.7484 3.08593 14.9109 2.24844 13.8779 2.24844ZM13.8938 6.74844L11.2484 4.10303L2.24844 13.103V15.7484H4.89384L13.8938 6.74844Z' fill='%239CA5AD'/%3e%3c/g%3e%3cdefs%3e%3cclipPath id='clip0_2904_21775'%3e%3crect width='18' height='18' fill='white'/%3e%3c/clipPath%3e%3c/defs%3e%3c/svg%3e ");
      background-repeat: no-repeat;
      background-size: cover;
      width: 18px;
      height: 18px;
      @media (max-width: $mobile) {
        width: 16px;
        height: 16px;
      }
    }
  }
}

.delivery-radio {
  display: inline-block;
  background-color: #fff;
  padding: 16px;
  border-radius: 15px;
  cursor: pointer;
  @media (max-width: $tablet) {
    padding: 12px;
  }

  .delivery-radio__info {
    position: relative;
    display: flex;
    flex-direction: column;
    padding-left: 38px;

    &::before {
      content: '';
      position: absolute;
      left: 0;
      top: 0;
      border: 1px solid #E4E7EC;
      width: 26px;
      height: 26px;
      border-radius: 50%;
    }
  }

  &__name {
    font-size: 18px;
    font-weight: 500;
    line-height: 21.6px;
    margin-bottom: 2px;
    @media (max-width: $mobile) {
      font-size: 16px;
      font-weight: 500;
      line-height: 24px;
    }
  }

  &__subinfo {
    font-size: 16px;
    font-weight: 500;
    line-height: 24px;
    color: #6B7280;
    @media (max-width: $mobile) {
      font-size: 14px;
      font-weight: 400;
      line-height: 21px;
    }
  }

  &__input {
    display: none;

    &:checked {
      & + .delivery-radio__info {
        &::after {
          content: '';
          position: absolute;
          left: 5px;
          top: 5px;
          width: 16px;
          height: 16px;
          background-color: $blue;
          border-radius: 50%;
        }
      }
    }
  }
}

.payment-item {
  input {
    display: none;

    &:checked + .payment-item__label {
      border: 1px solid #1EB2E8;

      &:hover {
        opacity: 1;
      }
    }
  }

  &__label {
    display: flex;
    height: 100%;
    padding: 16px;
    border-radius: 15px;
    border: 1px solid #E9ECF2;
    background-color: #fff;
    cursor: pointer;
    @include hover-effect;
  }

  &__icon {
    display: flex;
    align-items: flex-start;
    margin-right: 12px;
    flex-direction: column;
    width: 26px;
    flex-shrink: 0;

    img,
    svg {
      width: 100%;
      height: auto;
      object-fit: contain;
    }
  }

  &--active {
    border: 1px solid #1EB2E8;
  }

  &__descr {
    font-size: 16px;
    font-weight: 500;
    line-height: 24px;
    color: #6B7280;
    @media (max-width: $mobile) {
      font-size: 14px;
      line-height: 21px;
    }
  }

  &__title {
    font-size: 18px;
    font-weight: 500;
    line-height: 21.6px;
    @media (max-width: $mobile) {
      font-size: 16px;
      line-height: 24px;
    }
  }
}